import React, { useMemo } from 'react'
import { getBadgeColor } from '../../../../utils/common';
import { CustomTable } from '../../../reuseableComponents';

const DeductionsTable = (props) => {
    const { currentPage, setCurrentPage, deductionsData,handleToggleModals } = props


    const deductionField = [
        { key: 'sn', label: 'S.N' },
        { key: 'payslip_name', label: 'Payslip Name' },
        { key: 'name', label: 'Name' },
        { key: 'value', label: 'Value' },
        { key: 'value_in', label: 'Calculation Type' },
        { key: 'is_active', label: 'Status' },
        { key: 'action', label: 'Action' }
    ];
    const deductionScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        include_in_ctc: item => (
            <span
                className={`badge ${item.include_in_ctc ? getBadgeColor("active") : getBadgeColor("inactive")}`}
            >
                {item.include_in_ctc ? "Active" : "Inactive"}
            </span>
        ),
        is_active: item => (
            <span
                className={`badge ${item.is_active ? getBadgeColor("active") : getBadgeColor("inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),


        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">

                    <li>
                        <span className="dropdown-item"
                        onClick={() => handleToggleModals("deduction",item)}
                        >Update</span>
                    </li>
                </ul>
            </div>
        )
    }), [deductionsData, currentPage]);
    return (
        <>
            <CustomTable
                data={deductionsData?.data}
                fields={deductionField}
                // isLoading={}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={deductionsData?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={deductionScopedSlots}
            />
            {/* <div className="requestdata">
                <div class="d-flex align-items-center mb-4">
                    <span class="fw-bold fs-6 text-black">Deductions</span>
                    <ul class="nav nav-tabs leaveTabs ms-3">
                        <li class="nav-item">
                            <button class="nav-link active" data-bs-toggle="tab" data-bs-target="#pending"
                                // onClick={() => onHandleAppliedStatus("attendance_correction", "pending")}
                                type="button">
                                    Pre-Tax Deduction
                              
                            </button>
                        </li>
                        <li class="nav-item">
                            <button class="nav-link" data-bs-toggle="tab" data-bs-target="#pending"
                                type="button"
                                // onClick={() => onHandleAppliedStatus("attendance_correction", "approve")}
                            >
                             Post-Tax Deduction   
                            </button>
                        </li>
                    
                    </ul>
                </div>
                <div className="tab-content">
                    <div className="tab-pane fade active show" id="pending">

                        <CustomTable
                            data={deductionsData?.data}
                            fields={deductionField}
                            // isLoading={}
                            pagination={true}
                            currentPage={currentPage}
                            setCurrentPage={setCurrentPage}
                            totalPages={deductionsData?.totalPages}
                            // tableName={""}
                            // onSearch={handleSearch}
                            isHeader={false}
                            tableStyle={{ padding: "0px", boxShadow: "none" }}
                            scopedSlots={deductionScopedSlots}
                        />

                    </div>

                </div>
            </div> */}
        </>
    )
}

export default DeductionsTable

