import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { getBadgeColor } from '../../../../utils/common'
import { CustomButton } from '../../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../../translation'
import { Icon } from '../../../commanComponent'
import { useSelector } from 'react-redux'

const InvestmentCategoryTable = (props) => {
    const { addAllInvestmentCategoryPending, } = useSelector(state => state.payroll)
    const {
        isLoading,
        toggleCreateUpdateInvestmentCategoryModal,
        currentPage,
        setCurrentPage,
        investmentCategoryData,
        handleOpenCloseModals,
        toggleDeleteInvestmentModal
    } = props

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'category', label: 'Category Name' },
        { key: 'max_limit', label: 'Max Limit' },
        { key: 'max_limit_amount', label: 'Max Limit Amount' },
        { key: 'is_eligible_for_old', label: 'Eligible For Old' },
        { key: 'is_eligible_for_new', label: 'Eligible For New' },
        { key: 'action', label: 'Action' }
    ];


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        max_limit: item => (
            <span
                className={`badge ${getBadgeColor(item.max_limit ? "Active" : "Inactive")}`}
            >
                {item.max_limit? "Active" : "Inactive"}
            </span>
        ),
        is_eligible_for_old: item => (
            <span
                className={`badge ${getBadgeColor(item.is_eligible_for_old ? "Active" : "Inactive")}`}
            >
                {item.is_eligible_for_old ? "Active" : "Inactive"}
            </span>
        ),
        is_eligible_for_new: item => (
            <span
                className={`badge ${getBadgeColor(item.is_eligible_for_new ? "Active" : "Inactive")}`}
            >
             {item.is_eligible_for_new ? "Active" : "Inactive"}
            </span>
        ),
    action: item => (
        <div className="btn-group actionDrp">
            <button type="button" className="btn dropdown-toggle-split"
                data-bs-toggle="dropdown" aria-expanded="false">
                <i className="bi bi-three-dots-vertical"></i>
            </button>
            <ul className="dropdown-menu">
                <li>
                    <span className="dropdown-item"
                        onClick={() => handleOpenCloseModals("category",item)}
                    >
                        <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                </li>
                { <li>
                    <span className="dropdown-item"
                        onClick={() => toggleDeleteInvestmentModal("category",item)}
                    >
                        <Icon className={"bi bi-trash me-1"} />Delete</span>
                </li>}
            </ul>
        </div>
    )
}), [toggleCreateUpdateInvestmentCategoryModal]);



    return (
        <>
            <CustomTable
                data={investmentCategoryData?.data}
                fields={fields}
                isLoading={addAllInvestmentCategoryPending}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                // totalPages={depList?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={scopedSlots}
            />
        </>
    )
}

export default InvestmentCategoryTable
