import React from 'react'
import { CustCheckbox, CustomRadio, CustomStrong, Input } from '../../../commanComponent'
import { yesNoFlexOptions, yesNoOptions } from '../../../../utils/Constants.config'
import { useCustomTranslation } from '../../../../translation'

const ViewsEarningTypes = ({earningTypeData}) => {
    const {t}=useCustomTranslation()
    return (
        <div>
            <div className="row reviewsComment">
                <div className="col-md-12" >
                    <div className="form-group mb-2">
                        <CustomStrong
                            text={t("labels.earningTypeName")}
                            required={true}
                        />
                        <Input
                            type="text"
                            placeholder={t("placeholders.earningTypeName")}
                            value={earningTypeData?.earning_type_name}
                            name="earningTypeName"
                            isDisabled={true}
                        />
                    </div>
                </div>

                <div className="col-md-12">
                    <div className="form-group mb-2">
                        <CustomStrong
                            text={t("labels.calculationType")}
                            required={true}
                        />
                        <CustomRadio
                            data={yesNoOptions}
                            name="calculationType"
                            selectedValue={earningTypeData?.calculation_type}
                            isDisabled={true}
                        />
                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group mb-2">
                        <CustomStrong
                            text={"Calculate on pro-rata basis."}
                        />
                        <p>
                            ( Pay will be adjusted based on employee working days.)</p>
                        <CustomRadio
                            data={yesNoFlexOptions}
                            name="isProRata"
                            selectedValue={earningTypeData?.is_pro_rata}
                            isDisabled={true}
                        />
                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group mb-2">
                        <CustomStrong
                            text={"Include this as a Flexible Benefit Plan component."}
                        />
                        <p>
                            ( FBP allows your employees to personalise their salary structure by choosing how much they want to receive under each FBP component.)</p>
                        <CustomRadio
                            data={yesNoFlexOptions}
                            name="canIncludeAsFbp"
                            selectedValue={earningTypeData?.can_include_as_fbp}
                            isDisabled={true}
                        />
                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group mb-2">
                        <CustomStrong
                            text={"Consider for EPF Contribution."}
                        />
                        <CustomRadio
                            data={yesNoFlexOptions}
                            name="isIncludedInEpf"
                            selectedValue={earningTypeData?.is_included_in_epf}
                            isDisabled={true}
                        />
                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group mb-2">
                        <CustomStrong
                            text={"Consider for ESI Contribution."}
                        />
                        <CustomRadio
                            data={yesNoFlexOptions}
                            name="isIncludedInEsi"
                            selectedValue={earningTypeData?.is_included_in_esi}
                            isDisabled={true}
                        />
                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group mb-2">
                        <CustomStrong
                            text={"Show this component in payslip"}
                        />
                        <CustomRadio
                            data={yesNoFlexOptions}
                            name="showInPayslip"
                            selectedValue={earningTypeData?.show_in_payslip}
                            isDisabled={true}
                        />
                    </div>
                </div>
                <div className="col-md-12 d-flex">
                    <div className="form-group mb-3">
                        <CustCheckbox
                            key={"isActive"}
                            id={`isActive`}
                            checked={earningTypeData?.is_active}
                            name={"isActive"}
                        />

                    </div>
                    <div className="form-group mb-3">
                        <CustomStrong text={t("labels.isActive")} className="text-primary" />
                    </div>

                </div>


            </div>
        </div>
    )
}

export default ViewsEarningTypes
