import React, { useState } from 'react'
import { useCustomTranslation } from '../../../../translation'
import { useDispatch, useSelector } from 'react-redux'
import { CustomError, Label, Textarea } from '../../../commanComponent'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { CustomButton } from '../../../globalComponents'

const EarningRulesFormula = (props) => {
        const dispatch = useDispatch()
         const { 
            getAllEarningTypeRes,
          } = useSelector(state => state.payroll)
        const { toggleEarningRulesFormula, allLeaveConstantData, setEarningRulesData } = props
        const { t } = useCustomTranslation()
        const [earningRules, setEarningRules] = useState({
            brackets: '',
            earningType: '',
            operators: ''
        })
        const [validDescription, setValidDescription] = useState('')
        const [isFillDesc, setIsFillDesc] = useState(false)
    
        const handleFromChange = (e, fieldName) => {
            const name = fieldName
            const value = e;
    
            setEarningRules(prevState => {
                const updatedState = {
                    ...prevState,
                    [name]: value,
                };
                return updatedState;
            });
            setIsFillDesc(false)
        };
        const onAddFormulaField = () => {
            const { brackets, earningType, operators } = earningRules
            let addFormulaFields =
                (brackets?.value || '') +
                (earningType?.value || '') +
                (operators?.value || '');
    
            setValidDescription((prev) => prev + addFormulaFields);
            setEarningRules({
                brackets: '',
                earningType: '',
                operators: ''
            })
        }
    
        const checkEarningRulesFormula = () => {
            if (validDescription) {
                setIsFillDesc(false)
                const payload = {
                    formula: validDescription
                }
                // dispatch(leaveAction.checkLeaveEarningRulesRequest(payload))
            } else {
                setIsFillDesc(true)
            }
        }
        const saveEarningRules = () => {
            setEarningRulesData((pre) => ({
                ...pre,
                ["amount"]: validDescription
            }))
        }

    
  return (
    <div className='border p-2' style={{ backgroundColor: "#DCDCDC" }}>
            <div className='p-3' style={{ backgroundColor: "white" }}>
                <div className="header ps-2 d-flex justify-content-between">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{t("headings.amountFormula")}</h5>
                    <button type="button" className="btn-close" onClick={() => toggleEarningRulesFormula()}></button>
                </div>
                <hr className='m-1' />
                <div className="body mt-2">
                    <div className="formPanel">
                        <div className='row'>
                            <div className="col-md-3 form-group mb-3">
                                <Label text={t("labels.brackets")}
                                />
                                <CustomSelect
                                    data={allLeaveConstantData && allLeaveConstantData?.brackets}
                                    placeholder={t("placeholders.select")}
                                    value={earningRules?.brackets || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "brackets")}
                                    name="brackets"
                                />
                            </div>
                            <div className="col-md-3 form-group mb-3">
                                <Label text={t("labels.earningType")}
                                />
                                <CustomSelect
                                    data={getAllEarningTypeRes && getAllEarningTypeRes?.data ||[]}
                                    placeholder={t("placeholders.earningType")}
                                    value={earningRules?.earningType || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "earningType")}
                                    name="earningType"
                                />
                            </div>
                            <div className="col-md-3 form-group mb-3">
                                <Label text={t("labels.operators")}
                                />
                                <CustomSelect
                                    data={allLeaveConstantData && allLeaveConstantData?.operators}
                                    placeholder={t("placeholders.select")}
                                    value={earningRules?.operators || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "operators")}
                                    name="operators"
                                />
                            </div>
                            <div className="col-md-3 form-group mb-3">
                                <CustomButton
                                    text={t("btns.add")}
                                    classNames={"btn btn-org-gradient  mt-4"}
                                    handleClick={() => onAddFormulaField()}
                                />
                            </div>
                        </div>
                        <div className='row'>
                            <div className="col-md-9 form-group mb-3">
                                <Textarea
                                    value={validDescription}
                                    placeholder={t("placeholders.leaveExpression")}
                                    onChange={(e) => setValidDescription(e.target.value)}
                                />
                                {isFillDesc && (
                                    <CustomError text={"Place add formula to validate"} />
                                )}
                            </div>
                        </div>
                        <div className='row'>
                            <div className="col-md-9  mb-3 d-flex justify-content-end" >
                                <CustomButton
                                    text={t("btns.validate")}
                                    classNames={"btn btn-success gradientBg "}
                                    // handleClick={() => checkEarningRulesFormula()}
                                    // isLoading={checkLeaveEarningRulesPending}
                                />
                            </div>
                        </div>

                    </div>
                </div>
                <div className="footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={t("btns.save")}
                            classNames={"btn btn-primary mt-2"}
                            handleClick={() => saveEarningRules()}
                        />
                    </div>
                </div>
            </div>
        </div>
  )
}

export default EarningRulesFormula
