import React from 'react'


const CustomCards = ({cardData}) => {

    const renderedCards = () => {
        return (
            cardData && cardData?.length>0 && cardData.map((card)=>(
                <div className="col-md-2 px-md-2 mb-3"
                key={card?.id}
                onClick={()=> card?.onClickFunc(card?.id)}
                >
                <span
                    className="totalBx h-100"
                >
                    <figure>
                        <img src={card?.imgUrl} alt="" width={50} />
                    </figure>
                    <h3 className="fw-bolder my-0">{card?.name}</h3>
                    <img
                        src="images/firstlineBlue.svg"
                        className="cornerLine"
                        alt=""
                        width={95}
                    />
                </span>
            </div>
             ) ) 
           
        )
    }
    return (
        <>
            <div className="cardBox leaveAppli h-100 mb-4">
                <div className="row">
                    <div className="col-12 selectModal position-relative z-1">
                        <div className="row px-md-1 ">
                            {renderedCards()}
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default CustomCards
