import React, { useMemo } from 'react'
import { CustomButton } from '../../../globalComponents'
import { useCustomTranslation } from '../../../../translation'
import { CustomTable } from '../../../reuseableComponents'
import { useSelector } from 'react-redux'
import { Icon } from '../../../commanComponent'

const TaxSlab = (props) => {
    const { t } = useCustomTranslation()
    const { handleToggleModals, taxSlabData, currentPage, setCurrentPage, toggleDeleteTaxSlabModal } = props
    const { getTaxSlabPending } = useSelector(state => state.payroll)
    const taxSlabFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'type', label: 'Slab Type' },
        { key: 'start', label: 'Start' },
        { key: 'end', label: 'End' },
        { key: 'tex_percent', label: 'Tax Percent' },
        { key: 'action', label: 'Action' },

    ];
    const taxSlabScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">

                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleToggleModals("tax_slab", item)}
                        >
                            <Icon className={"fa fa-edit"} /> Update</span>
                    </li>
                    <li>
                        <span className="dropdown-item"
                            onClick={() => toggleDeleteTaxSlabModal(item?._id)}
                        >
                            <Icon className={"fa fa-trash"} /> Delete</span>
                    </li>
                </ul>
            </div>
        )

    }), []);
    return (
        <div>
            <div className="cardHead  justify-content-between">
                <div className='d-flex'>
                    <img src="/images/myrequest-icon.svg" alt="" width="35" />
                    <h3 className="ms-2 mt-2">Tax Slab</h3>
                </div>
                <div>
                    <CustomButton
                        text={t("btns.addTaxSlab")}
                        classNames="btn btn-primary"
                        handleClick={() => handleToggleModals("tax_slab")}
                    />

                </div>

            </div>
            <CustomTable
                data={taxSlabData?.data}
                fields={taxSlabFields}
                isLoading={getTaxSlabPending}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={(pageNo) => setCurrentPage(pageNo, "tax_slab")}
                totalPages={taxSlabData?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={taxSlabScopedSlots}
            />
        </div>
    )
}

export default TaxSlab
