import React, { useState } from 'react'
import { CustomTabs } from '../../../reuseableComponents';
import InvestmentCategoryTable from './InvestmentCategoryTable';
import InvestmentSubCategoryTable from './InvestmentSubCategoryTable';
import { Icon } from '../../../commanComponent';

const Investments = (props) => {
    const {
        handleOpenCloseModals,
        investmentCategoryData,
        toggleDeleteInvestmentModal,
        investmentSubCategoryData,
    } = props


    const tableTabsRequest = [
        { name: "Investment Category", onClickFun: (id) => getPayRunsData(id), id: "category" },
        { name: "Investment Sub Category", onClickFun: (id) => getPayRunsData(id), id: "sub_category" },

    ];


    const getPayRunsData = (id) => {

    }


    return (
        <div className="tab-pane fade show active" id="receivedFeedback">
            <div className="cardHead d-flex justify-content-between">
                <div className="d-flex align-items-center">
                    <img
                        src="images/kpi-guidline.svg"
                        alt=""
                        width={40}
                        className="me-2 fw-bold"
                    />
                    <div className="">
                        <h3>Investment </h3>
                    </div>
                </div>
                <div className="btn-group actionDrp">
                    <button type="button" className="btn dropdown-toggle-split"
                        data-bs-toggle="dropdown" aria-expanded="false">
                        <i className="bi bi-three-dots-vertical"></i>
                    </button>
                    <ul className="dropdown-menu">
                        <li>
                            <span className="dropdown-item"
                                onClick={() => handleOpenCloseModals("category")}
                            >
                                <Icon className={"fa fa-list  me-1"} />Add Category</span>
                        </li>
                        <li>
                            <span className="dropdown-item"
                                onClick={() => handleOpenCloseModals("sub_category")}
                            >
                                <Icon className={"fa fa-list-alt me-1"} />Add Sub Category</span>
                        </li>
                    </ul>
                </div>
            </div>
            <CustomTabs tabs={tableTabsRequest} type="rayRuns" />
            <div className="tab-content mykpiMain">
                <div className="tab-pane fade active show" id="category">

                    <InvestmentCategoryTable
                        investmentCategoryData={investmentCategoryData}
                        handleOpenCloseModals={handleOpenCloseModals}
                        toggleDeleteInvestmentModal={toggleDeleteInvestmentModal}
                    />
                </div>
                <div className="tab-pane fade" id="sub_category">

                    <InvestmentSubCategoryTable
                        investmentSubCategoryData={investmentSubCategoryData}
                        toggleDeleteInvestmentModal={toggleDeleteInvestmentModal}
                        handleOpenCloseModals={handleOpenCloseModals}


                    />
                </div>
            </div>

        </div>
    )
}

export default Investments
