import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { getBadgeColor } from '../../../../utils/common'
import { CustomButton } from '../../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../../translation'
import { Icon } from '../../../commanComponent'

const InvestmentSubCategoryTable = (props) => {
    const { t } = useCustomTranslation()
    const {
        isLoading,
        toggleCreateUpdateInvestmentSubCatModal,
        currentPage,
        setCurrentPage,
        investmentSubCategoryData,
        handleOpenCloseModals,
        toggleDeleteInvestmentModal
    } = props



    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => handleOpenCloseModals("sub_category", item)}
                        >
                            <Icon className={"bi bi-pencil-square me-1"} />Update</span>
                    </li>
                    {<li>
                        <span className="dropdown-item"
                            onClick={() => toggleDeleteInvestmentModal("sub_category", item)}
                        >
                            <Icon className={"bi bi-trash me-1"} />Delete</span>
                    </li>}
                </ul>
            </div>

        )
    }), [toggleCreateUpdateInvestmentSubCatModal]);


    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'category_name', label: 'Category' },
        { key: 'sub_category_name', label: 'Sub Category Name' },
        { key: 'input_type', label: 'Input type' },
        { key: 'action', label: 'Action' }
    ];
    return (
        <>
            <CustomTable
                data={investmentSubCategoryData?.data}
                fields={fields}
                // isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                // totalPages={depList?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={scopedSlots}
            />
        </>
    )
}

export default InvestmentSubCategoryTable
