import React, { useEffect, useState } from 'react'
import { CustCheckbox, CustomStrong, CustRadio, Input, Label } from '../../../commanComponent'
import { CustomButton, CustomError } from '../../../globalComponents'
import { useCustomTranslation } from '../../../../translation'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useDispatch, useSelector } from 'react-redux'
import { commonActions } from '../../../../core/resource/actions'

const AddUpdatePaySchedule = (props) => {
    const {
        isOpenModal,
        selectedPayScheduleData,
        handleToggleModals,
        appConstantData,
        handleAddPayScheduleData,
        handleUpdatePaySchedule
    } = props

    const { t } = useCustomTranslation()

    const [payScheduleFormData, setPayScheduleFormData] = useState({
        payScheduleType: 'monthly',
        payOn: "last_working_day",
        payDay:'',
        payPeriodStartDate:''
    })

    const [errors, setErrors] = useState('')
    const {addPaySchedulePending,updatePaySchedulePending}=useSelector(state=>state.payroll)


    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };
        setPayScheduleFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: value,
            };
            if(value ==="monthly"){
                updatedState["noOfWorkingDays"]=''
            }  if(value ==="last_working_day"){
                updatedState["payDay"]=''
            }
            return updatedState;
        });

        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };


    const submitAddUpdatePaySchedule = () => {
        const payload ={
            pay_schedule_type:payScheduleFormData?.payScheduleType,
            no_of_working_days:payScheduleFormData?.noOfWorkingDays?.value,
            pay_on:payScheduleFormData?.payOn,
            pay_day:payScheduleFormData?.payDay?.value ||'',
            // pay_date:payScheduleFormData?.payDay?.value ||'',
            pay_period_start_date:payScheduleFormData?.payPeriodStartDate,
        }
        if(selectedPayScheduleData){
            payload["_id"]= selectedPayScheduleData?.[0]?._id||''
            handleUpdatePaySchedule(payload)
        }else{
            handleAddPayScheduleData(payload)
        }
    }
    useEffect(()=>{
        if(selectedPayScheduleData&& selectedPayScheduleData?.length>0){
            const selectData =selectedPayScheduleData[0]
            setPayScheduleFormData({
                payScheduleType: selectData?.pay_schedule_type,
                noOfWorkingDays:{label:selectData?.no_of_working_days,value:selectData?.no_of_working_days},
                payOn:  selectData?.pay_on,
                payDay:{label:selectData?.pay_day,value:selectData?.pay_day},
                payPeriodStartDate:selectData?.pay_period_start_date
            })
        }

    },[selectedPayScheduleData])



    return (
        <>
            <div className={`offcanvas offcanvas-end  ${isOpenModal ? 'show' : ''}`} tabIndex={-1}
                style={{
                    visibility: isOpenModal ? 'visible' : 'hidden',
                    width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> {selectedPayScheduleData ? "Update" : ""} Pay Schedule</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleToggleModals("pay_schedule")}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">

                        <div>
                            <CustomStrong
                                text={" Calculate monthly salary based on"}
                                required={true}
                                tooltipText={t("tooltips.calculateMonthlySalary")}
                                className={"text-primary"}
                            />
                            <div >
                                <div>
                                    <CustRadio
                                        htmlFor="monthly"
                                        text="Actual days in a month"
                                        name="payScheduleType"
                                        value="monthly"
                                        checked={payScheduleFormData?.payScheduleType === 'monthly'}
                                        handleChange={(e) => handleFromChange(e)}
                                        radioClass={"mt-2"}
                                    />

                                </div>
                                <div className='d-flex'>
                                    <CustRadio
                                        htmlFor="workingDays"
                                        text="Organisation working days"
                                        name="payScheduleType"
                                        value="working_days"
                                        checked={payScheduleFormData?.payScheduleType === 'working_days'}
                                        handleChange={(e) => handleFromChange(e)}
                                        radioClass={"mt-2"}
                                    />
                                    <div className='ms-2 me-2' style={{ width: "113px" }}>
                                        <CustomSelect
                                            data={appConstantData?.workingDays}
                                            placeholder={t("placeholders.select")}
                                            value={payScheduleFormData?.noOfWorkingDays}
                                            handleChange={(selectedOption) => handleFromChange(selectedOption, "noOfWorkingDays")}
                                            isDisabled={payScheduleFormData?.payScheduleType === 'monthly'}
                                        />
                                    </div>
                                    <label className='mt-2'>
                                        days per month
                                    </label>
                                </div>
                            </div>
                        </div>
                        <div className='mt-3'>
                            <CustomStrong
                                text={" Pay On"}
                                required={true}
                                className={"text-primary"}
                            />
                            <div>
                                <CustRadio
                                    htmlFor="lastWorkingDay"
                                    text="the last working day of"
                                    name="payOn"
                                    value="last_working_day"
                                    checked={payScheduleFormData?.payOn === 'last_working_day'}
                                    handleChange={(e) => handleFromChange(e)}
                                    radioClass={"mt-2"}
                                />

                            </div>
                            <div className='d-flex'>
                                <CustRadio
                                    htmlFor="payMonth"
                                    text="day"
                                    name="payOn"
                                    value="day_of_month"
                                    checked={payScheduleFormData?.payOn === 'day_of_month'}
                                    handleChange={(e) => handleFromChange(e)}
                                    radioClass={"mt-2"}
                                />
                                <div className='ms-2 me-2' style={{ width: "113px" }}>

                                    <CustomSelect
                                        data={appConstantData?.monthDays}
                                        placeholder={t("placeholders.select")}
                                        value={payScheduleFormData?.payDay}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "payDay")}
                                        isDisabled={payScheduleFormData?.payOn === 'last_working_day'}
                                    />
                                </div>
                                <label className='mt-2'>
                                    of every month
                                </label>
                            </div>
                        </div>
                        {/* <div class="alert alert-secondary mt-4" role="alert">
                            Note: When payday falls on a non-working day or a holiday, employees will get paid on the previous working day.
                        </div> */}

                        <div className="row mt-3">

                            <div className="col-md-6 row">
                                <div className='col-md-12'>
                                    <div className="form-group mb-2">
                                        <CustomStrong
                                            text={"Start your first payroll from"}
                                            required={true}
                                            className={"text-primary "} />
                                        <Input
                                            type="month"
                                            value={payScheduleFormData?.payPeriodStartDate}
                                            name="payPeriodStartDate"
                                            className="mt-2"
                                            onChange={(e) => handleFromChange(e)}
                                        />
                                        {errors?.name && (
                                            <CustomError text={errors.name} />
                                        )}
                                    </div>
                                </div>

                            </div>



                        </div>
                    </div>


                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedPayScheduleData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitAddUpdatePaySchedule()}
                        isLoading={selectedPayScheduleData ? updatePaySchedulePending : addPaySchedulePending}
                        />
                    </div>
                </div>
            </div>
            {isOpenModal && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddUpdatePaySchedule
