import React, { useEffect, useState } from 'react'
import { CustCheckbox, CustomRadio, Icon, Input, Label } from '../../../commanComponent'
import { CustomButton, CustomError } from '../../../globalComponents'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../../translation'
import { deductionFrequency } from '../../../../utils/Constants.config'

const AddPostTaxDeduction = (props) => {
    const { t } = useCustomTranslation()
    const {
        openModel,
        handleOpenClose,

    } = props
    const [errors, setErrors] = useState({})
    const [postTaxDeductionFormData, setPostTaxDeductionFormData] = useState({

        nameInPayslip: '',
        deductionFrequency: '',
        is_active: true
    })


    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setPostTaxDeductionFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Clear error for the specific field being changed
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitPostTaxDeduction = async () => {

    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])



    const resetForm = () => {
        setPostTaxDeductionFormData({
            nameInPayslip: '',
            deductionFrequency: '',
            is_active: true
        })
        setErrors({})
    }

    return (
        <>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "600px"
            }}
            id="interviewDetail"
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Post Tax Deduction</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={
                        () => handleOpenClose("post_tax_deduction")
                    }
                />
            </div>

            <div className="offcanvas-body myRequest ">
                <div className="formPanel">
                    <div className="row">

                        <div className="col-md-12">
                            <div className="form-group mb-2">
                                <Label text={t("labels.nameInPayslip")} required={true} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.nameInPayslip")}
                                    value={postTaxDeductionFormData?.nameInPayslip}
                                    name="nameInPayslip"
                                    onChange={(e) => handleFromChange(e)}
                                />

                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-2">
                                <Label text={t("labels.deductionFrequency")} required={true} />
                                <CustomRadio
                                    data={deductionFrequency}
                                    name="calculationType"
                                    handleChange={handleFromChange}
                                    selectedValue={postTaxDeductionFormData?.deductionFrequency}
                                // radioStylxe={{ display: "flex" }}
                                />
                            </div>
                        </div>
                        <div className="col-md-3">
                            <div className="form-group mb-3">
                                <Label text={t("labels.isActive")} />
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="form-group mb-3">
                                <CustCheckbox
                                    key={"is_active"}
                                    id={`is_active`}
                                    // label={"Active"}
                                    checked={postTaxDeductionFormData?.is_active}
                                    handleChange={(e) => handleFromChange(e)}
                                    name={"is_active"}
                                />

                            </div>
                        </div>
                        <div className="col-md-12 mt-3">
                            <div className="form-group mb-3">
                                <div class="alert alert-warning" role="alert">
                                    <b>Note:</b>{t("paragraph.postTaxDeduction")}
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={t("btns.submit")}
                        classNames={"btn btn-org-gradient"}
                        handleClick={() => submitPostTaxDeduction()}
                    // isLoading={selectedDeductionData ? updateDeductionPending : createDeductionPending}
                    />
                </div>
            </div>
        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddPostTaxDeduction
