import React, { useEffect, useState } from 'react'
import { useCustomTranslation } from '../../../../translation'
import { CustomStrong, Input } from '../../../commanComponent'
import { CustomButton } from '../../../globalComponents'
import EarningRulesTable from './EarningRulesTable'
import EarningRulesFormula from './EarningRulesFormula'
import CustomAlert from '../../../commanComponent/CustomAlert'

const EarningRules = (props) => {
    const { allLeaveConstantData, setRules, rules } = props
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState('')
    const [isOpenERF, setIsOpenERF] = useState('')
    const [earningRuleData, setEarningRuleData] = useState([])
    const [selectedIndex, setSelectedIndex] = useState('')
    const [earningRules, setEarningRules] = useState({
        from: "",
        to: '',
        amount: ""
    })


    const handleFromChange = (e, fieldName, index) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setEarningRules(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Clear error for the specific field being changed
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));

    };
    const deleteEarningRule = (index) => {
        const earningData = earningRuleData?.filter((_, ind) => ind !== index)
        setEarningRuleData(earningData)

    }
    const editEarningRule = (item,index) => {
        setEarningRules({
            from: item?.from,
            to: item?.to,
            amount: item?.amount
        })
        setSelectedIndex(index)
    }
    const submitEarningRules = () => {
        if (selectedIndex !== '') {
            const updatedRuleData = earningRuleData.map((rule, index) => {
                if (index === selectedIndex) {
                    return {
                        ...rule,
                        from: earningRules?.from,
                        to: earningRules?.to,
                        amount: earningRules?.amount,
                    };
                }
                return rule;
            });
    
            setEarningRuleData(updatedRuleData);
            setRules(updatedRuleData);
            setSelectedIndex('')
        } else {
            setEarningRuleData([...earningRuleData, earningRules])
            setRules([...earningRuleData, earningRules])
        }
        resetEarningRulesFields()

    }
    const toggleEarningRulesFormula = () => {
        setIsOpenERF(!isOpenERF)

    }

    const resetEarningRulesFields = () => {
        setEarningRules({
            from: "",
            to: '',
            amount: ""
        })
    }
    useEffect(() => {
        if (rules) {
            setEarningRuleData(rules)
        }
    }, [rules])
    return (
        <>
            {earningRuleData && earningRuleData.length > 0 && <EarningRulesTable
                earningRuleData={earningRuleData}
                deleteEarningRule={deleteEarningRule}
                rules={rules}
                editEarningRule={editEarningRule}
            />}

            <div className="col-md-12 row">
                <div className="form-group mb-2">
                    <CustomStrong
                        className={"text-primary"}
                        text={t("labels.rules")}
                        required={true}
                    />
                    <div className="col-md-12 mt-2">
                        <div className="form-group mb-2 d-flex justify-content-between gap-3">
                            <span className='rupee-sign' >&#8377;</span>

                            <Input
                                type="number"
                                placeholder={t("placeholders.startSalary")}
                                value={earningRules?.from}
                                name="from"
                                onChange={(e) => handleFromChange(e)}
                            />
                            <span className='mt-2' style={{ fontSize: "16px" }} >To</span>
                            <Input
                                type="number"
                                placeholder={t("placeholders.endSalary")}
                                value={earningRules?.to}
                                name="to"
                                onChange={(e) => handleFromChange(e)}
                            />
                        </div>
                    </div>
                </div>
            </div>
            <div className="col-md-12 row">
                <div className="form-group mb-2">

                    <div className="col-md-12 mt-2">
                        <div className="form-group mb-2 d-flex justify-content-start gap-3">
                            <span className='rupee-sign' >&#8377;</span>

                            <Input
                                type="text"
                                placeholder={t("placeholders.amountFormula")}
                                value={earningRules?.amount}
                                name="amount"
                                onChange={(e) => handleFromChange(e)}
                                style={{ width: "43%" }}
                            />
                            <CustomButton
                                text={t("btns.createFormula")}
                                classNames={"btn btn-primary"}
                                handleClick={() => toggleEarningRulesFormula()}
                            />

                        </div>
                    </div>
                </div>
            </div>
            {isOpenERF && <EarningRulesFormula
                allLeaveConstantData={allLeaveConstantData}
                toggleEarningRulesFormula={toggleEarningRulesFormula}
                setEarningRulesData={setEarningRules}

            />}
            <CustomAlert alertLabel={"Note: "} text={t("paragraph.addEarningRules")} />
            <hr />
            <div className='d-flex justify-content-end'>
                <CustomButton
                    text={selectedIndex !=='' ?t("btns.update"):t("btns.addRules")}
                    classNames={"btn btn-success"}
                    styles={{ minWidth: "0px" }}
                    handleClick={() => submitEarningRules()}
                />

            </div>
        </>

    )
}

export default EarningRules
