import React from 'react'
import { CustomTabs } from '../../../reuseableComponents';
import EarningsTable from './EarningsTable';
import DeductionsTable from './DeductionsTable';

const SalaryComponent = (props) => {
    const {handleToggleModals,earningData,deductionsData}=props
    const tableTabsRequest = [
        { name: "Earnings", onClickFun: (id) => getSalaryData(id), id: "earnings" },
        { name: "Deductions", onClickFun: (id) => getSalaryData(id), id: "deductions" },
    ];
    const getSalaryData = (id) => {

    }
    return (
        <div>
            <div className="cardBox myRequest mb-3 employeeHistory">
                <div className="cardHead  justify-content-between">
                    <div className='d-flex'>
                        <img src="/images/myrequest-icon.svg" alt="" width="35" />
                        <h3 className="ms-2 mt-2">Salary Component</h3>
                    </div>
                    <div>
                        <div className="dropdown payrollSetting">
                            <button
                                className="btn btn-primary"
                                type="button"
                                data-bs-toggle="dropdown"
                                aria-expanded="true"
                            >
                                Add Component
                            </button>
                            <ul
                                className="dropdown-menu dropdownLeft"
                                data-popper-placement="top-end"
                      
                            >
                                <li className="dropdown-item"
                                onClick={()=>handleToggleModals("add_earnings")}
                                >
                                    <span>Add Earnings</span>
                                </li>
                                <li className="dropdown-item"
                                onClick={()=>handleToggleModals("deduction")}
                                >
                                    <span>Deduction</span>
                                </li>
                                {/* <li className="dropdown-item"
                                onClick={()=>handleToggleModals("post_tax_deduction")}
                                >
                                    <span>Post-Tax Deduction</span>
                                </li> */}
                                {/* <li className="dropdown-item"
                                onClick={()=>handleToggleModals("pre_tax_deduction")}
                                >
                                    <span>Pre-Tax Deduction</span>
                                </li> */}
                            </ul>
                        </div>

                    </div>
                </div>
                <CustomTabs tabs={tableTabsRequest} type="employeeHistory" />
                <div className="tab-content mykpiMain">
                    <div className="tab-pane fade active show" id="earnings">
                        <EarningsTable
                        earningData={earningData}
                        handleToggleModals={handleToggleModals}
                        />
                    </div>
                    <div className="tab-pane fade" id="deductions">
                        <DeductionsTable
                        deductionsData={deductionsData}
                        handleToggleModals={handleToggleModals}
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SalaryComponent
