import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { useCustomTranslation } from '../../../../translation'
import { CustCheckbox, Input, Label } from '../../../commanComponent'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { validateSchema } from '../../../../utils/ValidationHelper'
import { taxSlabSchema } from '../../../../utils/ValidationSchema'
import { useSelector } from 'react-redux'

const AddUpdateTaxSlab = (props) => {
    const {
        isOpenModal,
        handleToggleModals,
        selectedTaxSlabData,
        filteredDropdownData,
        handleAddUpdateTaxSlab
    } = props
    const { updateTaxSlabPending, addTaxSlabPending } = useSelector(state => state.payroll)
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState('')
    const [taxSlabFormData, setTaxSlabFormData] = useState({
        slabType: null,
        start: '',
        end: '',
        taxPercent: '',
        includeCess: false,
        cessPercent: '',
        standardDeduction: false,
        standardDeductionValue: '',
        taxRelaxation: '',
        constantDifference: '',
        rebateAllowed: false,
        rebateRules: false,
        isActive: true

    })
    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setTaxSlabFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };

            return updatedState;
        });
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    }


    const submitAddUpdateTaxSlab = async () => {
        const validationResponse = await validateSchema(taxSlabSchema, taxSlabFormData);
        if (validationResponse.isValid) {
            const {
                slabType,
                start,
                end,
                taxPercent,
                includeCess,
                cessPercent,
                standardDeduction,
                standardDeductionValue,
                taxRelaxation,
                constantDifference,
                rebateAllowed,
                rebateRules,
                isActive,

            } = taxSlabFormData
            let payload = {
                type: slabType?.label|| "",
                start,
                end,
                tex_percent: taxPercent,
                is_include_cess: includeCess,
                cess_percent: cessPercent || 0,
                is_standard_deduction: standardDeduction,
                standard_deduction_value: standardDeductionValue || 0,
                tex_relaxation: taxRelaxation,
                constant_difference: constantDifference,
                is_rebate_allowed: rebateAllowed,
                rebate_rules: rebateRules || {},
                is_active: isActive,
            }
            if (selectedTaxSlabData) {
                payload["_id"] = selectedTaxSlabData?._id;
                handleAddUpdateTaxSlab(payload)
            } else {

                handleAddUpdateTaxSlab(payload)
            }
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        if (selectedTaxSlabData) {
            setTaxSlabFormData({
                slabType: { label: selectedTaxSlabData?.type, value: selectedTaxSlabData?.type },
                start: selectedTaxSlabData?.start,
                end: selectedTaxSlabData?.end,
                taxPercent: selectedTaxSlabData?.tex_percent,
                includeCess: selectedTaxSlabData?.is_include_cess,
                cessPercent: selectedTaxSlabData?.cess_percent,
                standardDeduction: selectedTaxSlabData?.is_standard_deduction,
                standardDeductionValue: selectedTaxSlabData?.standard_deduction_value,
                taxRelaxation: selectedTaxSlabData?.tex_relaxation,
                constantDifference: selectedTaxSlabData?.constant_difference,
                rebateAllowed: selectedTaxSlabData?.is_rebate_allowed,
                rebateRules: selectedTaxSlabData?.rebate_rules,
                isActive: selectedTaxSlabData?.is_active,
            })
        }
    }, [selectedTaxSlabData])



    return (
        <>
            <div className={`offcanvas offcanvas-end  ${isOpenModal ? 'show' : ''}`} tabIndex={-1}
                style={{
                    visibility: isOpenModal ? 'visible' : 'hidden',
                    width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary"> {selectedTaxSlabData ? "Update" : "Add"} Tax Slab</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleToggleModals("tax_slab")}
                    />
                </div>
                <div className="offcanvas-body">

                    <div className="row">

                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.slabType")} required={true} />
                                <CustomSelect
                                    data={filteredDropdownData?.tax_slab}
                                    placeholder={t("placeholders.slabType")}
                                    value={taxSlabFormData?.slabType || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "slabType")}
                                    name="slabType"
                                />
                                {errors?.slabType && (
                                    <CustomError text={errors.slabType} />
                                )}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.startAmount")} required={true} />
                                <Input
                                    type="number"
                                    placeholder={t("placeholders.taxSlabStartAmount")}
                                    value={taxSlabFormData?.start === 0 ? '0' : taxSlabFormData?.start}
                                    name="start"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.start && (
                                    <CustomError text={errors.start} />
                                )}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.endAmount")} required={true} />
                                <Input
                                    type="number"
                                    placeholder={t("placeholders.taxSlabEndAmount")}
                                    value={taxSlabFormData?.end === 0 ? '0' : taxSlabFormData?.end}
                                    name="end"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.end && (
                                    <CustomError text={errors.end} />
                                )}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="form-group mb-2">
                                <Label text={t("labels.taxPercent")} required={true} />
                                <Input
                                    type="number"
                                    placeholder={t("placeholders.taxPercent")}
                                    value={taxSlabFormData?.taxPercent === 0 ? '0' : taxSlabFormData?.taxPercent}
                                    name="taxPercent"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.taxPercent && (
                                    <CustomError text={errors.taxPercent} />
                                )}
                            </div>
                        </div>
                        <div className="col-md-6 ">
                            <div className="form-group mb-2">
                                <Label text={t("labels.taxRelaxation")} required={true} />
                                <Input
                                    type="number"
                                    placeholder={t("placeholders.taxRelaxation")}
                                    value={taxSlabFormData?.taxRelaxation === 0 ? "0" : taxSlabFormData?.taxRelaxation}
                                    name="taxRelaxation"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.taxRelaxation && (
                                    <CustomError text={errors.taxRelaxation} />
                                )}
                            </div>
                        </div>
                        <div className="col-md-6 ">
                            <div className="form-group mb-2">
                                <Label text={t("labels.constantDifference")} required={true} />
                                <Input
                                    type="number"
                                    placeholder={t("placeholders.constantDifference")}
                                    value={taxSlabFormData?.constantDifference === 0 ? '0' : taxSlabFormData?.constantDifference}
                                    name="constantDifference"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.constantDifference && (
                                    <CustomError text={errors.constantDifference} />
                                )}
                            </div>
                        </div>

                        <div className="col-md-12 row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.includeCess")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"includeCess"}
                                        id={`includeCess`}
                                        // label={"Active"}
                                        checked={taxSlabFormData?.includeCess}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"includeCess"}
                                    />

                                </div>
                            </div>

                        </div>
                        {taxSlabFormData?.includeCess && <div className="col-md-12 row">
                            <div className="col-md-6 row">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.cessPercent")} required={true} />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.cessPercent")}
                                        value={taxSlabFormData?.cessPercent === 0 ? '0' : taxSlabFormData?.cessPercent}
                                        name="cessPercent"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.cessPercent && (
                                        <CustomError text={errors.cessPercent} />
                                    )}
                                </div>
                            </div>
                        </div>}

                        <div className="col-md-12 row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.standardDeduction")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"standardDeduction"}
                                        id={`standardDeduction`}
                                        // label={"Active"}
                                        checked={taxSlabFormData?.standardDeduction}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"standardDeduction"}
                                    />

                                </div>
                            </div>
                        </div>
                        {taxSlabFormData?.standardDeduction && <div className="col-md-12 row">
                            <div className="col-md-6 row">
                                <div className="form-group mb-2">
                                    <Label text={t("labels.standardDeductionAmount")} required={true} />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.standardDeduction")}
                                        value={taxSlabFormData?.standardDeductionValue === 0 ? "0" : taxSlabFormData?.standardDeductionValue}
                                        name="standardDeductionValue"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {/* {errors?.standardDeductionValue && (
                                        <CustomError text={errors.standardDeductionValue} />
                                    )} */}
                                </div>
                            </div>
                        </div>}
                        <div className="col-md-12 row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.rebateAllowed")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"rebateAllowed"}
                                        id={`rebateAllowed`}
                                        // label={"Active"}
                                        checked={taxSlabFormData?.rebateAllowed}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"rebateAllowed"}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12 row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"isActive"}
                                        id={`isActive`}
                                        // label={"Active"}
                                        checked={taxSlabFormData?.isActive}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"isActive"}
                                    />

                                </div>
                            </div>
                        </div>


                    </div>

                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedTaxSlabData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitAddUpdateTaxSlab()}
                            isLoading={selectedTaxSlabData ? updateTaxSlabPending : addTaxSlabPending}
                        />
                    </div>
                </div>
            </div>
            {isOpenModal && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddUpdateTaxSlab
