import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { getBadgeColor } from '../../../../utils/common';

const EarningsTable = (props) => {
    const { currentPage, setCurrentPage, earningData,handleToggleModals } = props

    const earningField = [
        { key: 'sn', label: 'S.N' },
        { key: 'payslip_name', label: 'Payslip Name' },
        { key: 'name', label: 'Name' },
        { key: 'value', label: 'Value' },
        { key: 'value_in', label: 'Calculation Type' },
        { key: 'is_active', label: 'Status' },
        { key: 'action', label: 'Action' }
    ];
    const earningScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        is_active: item => (
            <span
                className={`badge ${item.is_active ? getBadgeColor("active") : getBadgeColor("inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),


        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">

                    <li>
                        <span className="dropdown-item"
                        onClick={() =>handleToggleModals("add_earnings",item)}
                        >Update</span>
                    </li>
                </ul>
            </div>
        )
    }), [earningData, currentPage]);
    return (
        <div>
            <CustomTable
                data={earningData?.data}
                fields={earningField}
                // isLoading={}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={earningData?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={earningScopedSlots}
            />
        </div>
    )
}

export default EarningsTable
