import React, { useEffect, useMemo, useState } from 'react'
import { useCustomTranslation } from '../../../../translation';
import { CustomButton } from '../../../globalComponents';
import { CustomStrong, Icon, Label } from '../../../commanComponent';
import { CustomTable } from '../../../reuseableComponents';
import AddUpdatePaySchedule from './AddUpdatePaySchedule';
import { useDispatch, useSelector } from 'react-redux';
import { commonActions } from '../../../../core/resource/actions';
import { dateFormate } from '../../../../utils/common';

const PaySchedule = (props) => {
    const { appConstantData, upcomingPayrollData, handleToggleModals } = props
    const { t } = useCustomTranslation()
    const dispatch = useDispatch()
    const { upcomingPayrollsPending } = useSelector(state => state.payroll)

    const [payScheduleData, setPayScheduleData] = useState('')
    useEffect(() => {
        if (upcomingPayrollData && upcomingPayrollData?.length > 0) {
            setPayScheduleData(upcomingPayrollData[0])
        }
    }, [upcomingPayrollData])

    const arrearsEmployeeFields = [
        { key: 'pay_period_formatted', label: 'PAY PERIOD' },
        { key: 'pay_date_formatted', label: 'PAY DATE' },

    ];
    const arrearsEmployeeScopedSlots = useMemo(() => ({

        // pay_period_formatted: item => (
        //     <span
        //         className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
        //     >
        //         {item.is_active ? "Active" : "Inactive"}
        //     </span>
        // ),

    }), [upcomingPayrollData]);


    return (
        <div>

            <div className=' p-3'>
                <div className="d-flex mb-2 paySchedule">
                    <h3 className='me-1'
                        style={{ fontSize: "18px", fontWeight: 600 }}
                    >
                        This Organization's payroll runs on this schedule
                    </h3>
                    <div
                        onClick={() => handleToggleModals("pay_schedule", upcomingPayrollData)}
                        className='editIconBg'
                        > {upcomingPayrollData
                            ?
                            <Icon className={"bi bi-pencil text-white"} />
                            :
                            <Icon className={"bi bi-plus-lg"} />
                            }
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-2">
                        <div className="form-group mb-3">
                            <Label text={t("labels.payFrequency")} />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="form-group mb-3">
                            <CustomStrong text={payScheduleData?.pay_schedule_type || "-"} />
                        </div>
                    </div>
                </div>
                {/* <div className="row">
                    <div className="col-md-2">
                        <div className="form-group mb-3">
                            <Label text={t("labels.workingDays")} />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="form-group mb-3">
                            <CustomStrong text={"Tue,Wed,Thu,Fri"} />
                        </div>
                    </div>
                </div> */}
                <div className="row">
                    <div className="col-md-2">
                        <div className="form-group mb-3">
                            <Label text={t("labels.payDay")} />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="form-group mb-3">
                            <CustomStrong text={payScheduleData?.pay_day || "-"} />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-2">
                        <div className="form-group mb-3">
                            <Label text={t("labels.firstPayPeriod")} />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="form-group mb-3">
                            <CustomStrong
                                text={payScheduleData ? dateFormate(payScheduleData?.pay_period_start_date, "DD/MM/YYYY") : "-"}
                            />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-2">
                        <div className="form-group mb-3">
                            <Label text={t("labels.firstPayDate")} />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="form-group mb-3">
                            <CustomStrong
                                text={payScheduleData?.pay_date ? dateFormate(payScheduleData?.pay_date, "DD/MM/YYYY") : "-"}
                            />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 form-group mb-3">
                        <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Upcoming Payroll</h3>
                        <CustomTable
                            data={payScheduleData?.upcoming_pay_periods}
                            fields={arrearsEmployeeFields}
                            isLoading={upcomingPayrollsPending}
                            pagination={true}
                            // currentPage={currentPage}
                            // setCurrentPage={setCurrentPage}
                            // totalPages={depList?.totalPages}
                            // tableName={""}
                            // onSearch={handleSearch}
                            isHeader={false}
                            tableStyle={{ padding: "0px", boxShadow: "none" }}
                            scopedSlots={arrearsEmployeeScopedSlots}
                        />

                    </div>
                </div>

            </div>

        </div>
    )
}

export default PaySchedule
