import React, { useEffect, useState } from 'react'
import { CustCheckbox, CustomRadio, CustomStrong, Icon, Input, Label } from '../../../commanComponent'
import { CustomButton, CustomError } from '../../../globalComponents'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../../translation'
import { calculationTypeOptions } from '../../../../utils/Constants.config'
import EarningRules from './EarningRules'
import { CustomTabs } from '../../../reuseableComponents'
import ViewsEarningTypes from '../../earningDeductionTypes/earningDeductionType/ViewsEarningTypes'
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux'
import { payrollActions } from '../../../../core/resource/actions'
const AddUpdateEarnings = (props) => {
    const { t } = useCustomTranslation()
    const dispatch = useDispatch()
    const { getEarningTypeByIdRes,addEarningsPending,updateEarningsPending } = useSelector(state => state.payroll)
    const {
        earningDeductionSelectedData,
        openModel,
        handleOpenClose,
        allEarningTypeData,
        addEarnings,
        allLeaveConstantData,
        getEarningTypeById,
        isOpenModalType,
        addDeductions,
        updateEarnings,
        updateDeductions,
        earningTypeByIdData,
        allDeductionTypeData
    } = props
    const [errors, setErrors] = useState({})
    const [createUpdateEarningsFormData, setCreateUpdateEarningsFormData] = useState({
        earningType: null,
        nameInPayslip: '',
        calculationType: '',
        calculationTypeValue: '',
        // earningName: '',
        variablePay: false,
        includedCTC: false,
        taxableComp: false,
        isActive: true
    })
    const [rules, setRules] = useState([])


    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e?.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };
        setCreateUpdateEarningsFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            if (name === "earningType") {
                getEarningTypeById(e?._id)
            }

            if (name === "calculationType") {
                updatedState = {
                    ...updatedState,
                    calculationTypeValue: '', 
                };
            }

            return updatedState;
        });
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateEarnings = async () => {
        let payload = {
            payslip_name: createUpdateEarningsFormData?.nameInPayslip,
            // name: createUpdateEarningsFormData?.earningName,
            [isOpenModalType === "earnings" ? "earning_type" : "deduction_type"]:
            createUpdateEarningsFormData?.earningType?._id,
            value: createUpdateEarningsFormData?.calculationTypeValue,
            value_in: createUpdateEarningsFormData?.calculationType,
            is_optional: false,
            max_allowance: createUpdateEarningsFormData?.maxAllowance,
            include_in_ctc: createUpdateEarningsFormData?.includedCTC,
            rules: rules,
            is_variable_pay: createUpdateEarningsFormData?.variablePay,
            is_taxable: createUpdateEarningsFormData?.taxableComp,
            is_active: createUpdateEarningsFormData?.isActive
        }
        if(earningDeductionSelectedData){
            payload["_id"]=earningDeductionSelectedData?._id
            isOpenModalType == "earnings" ? updateEarnings(payload) : updateDeductions(payload)
        }else{
            
            isOpenModalType == "earnings" ? addEarnings(payload) : addDeductions(payload)
        }
    }
    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (earningDeductionSelectedData) {
            setCreateUpdateEarningsFormData({
                earningType: { label: earningDeductionSelectedData?.earning_type_name, value: earningDeductionSelectedData?.earning_type },
                nameInPayslip: earningDeductionSelectedData?.payslip_name,
                calculationType: earningDeductionSelectedData?.value_in,
                calculationTypeValue: earningDeductionSelectedData?.value,
                // earningName: earningDeductionSelectedData?.name,
                variablePay: earningDeductionSelectedData?.is_variable_pay,
                includedCTC: earningDeductionSelectedData?.include_in_ctc,
                taxableComp: earningDeductionSelectedData?.is_taxable,
                isActive: earningDeductionSelectedData?.is_active,

            })
            setRules(earningDeductionSelectedData?.rules)
        }
    }, [earningDeductionSelectedData])

    const resetForm = () => {
        setCreateUpdateEarningsFormData({
            earningType: null,
            nameInPayslip: '',
            calculationType: '',
            calculationTypeValue: '',
            // earningName: '',
            variablePay: "",
            includedCTC: false,
            taxableComp: false,
            isActive: true
        })
        setErrors({})
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_EARNING_TYPE_BY_ID_STORE"))
    }

    const earningTabs = [
        { name: "Earnings", onClickFun: (id) => onTabChange(id), id: "add_earnings" },
        { name: "Earning Rules", onClickFun: (id) => onTabChange(id), id: "earning_rules" },
        { name: "Other Configurations", onClickFun: (id) => onTabChange(id), id: "other_configurations" },
    ];

    const deductionTabs = [
        { name: "Deduction", onClickFun: (id) => onTabChange(id), id: "deduction" },
        { name: "Deduction Rules", onClickFun: (id) => onTabChange(id), id: "deduction_rules" },
        { name: "Other Configurations", onClickFun: (id) => onTabChange(id), id: "other_configurations" },
    ];
    const onTabChange = () => {

    }
    const checkCalculationType = () => {
        switch (createUpdateEarningsFormData?.calculationType) {
            case "percentage":
                return {
                    label: t("labels.percentage"),
                    placeholder: t("placeholders.percentage")
                };
            default:
                return {
                    label: t("labels.amount"),
                    placeholder: t("placeholders.amount")
                };
        }
    };
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        {earningDeductionSelectedData ? "Update" : "Add"} {isOpenModalType == "earnings" ? "Earnings" : "Deduction"}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("add_earnings")}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <CustomTabs tabs={isOpenModalType == "earnings" ? earningTabs : deductionTabs} type="earnings" />
                    <div className="tab-content ">
                        <div className="tab-pane fade active show" id={isOpenModalType == "earnings" ? `add_earnings` : 'deduction'}>
                            <div className="formPanel">
                                <div className='row mb-3 pb-2' style={{ borderBottom: "1px #C4C1BF solid" }}>
                                    <div className="col-md-6" >
                                        <div className="form-group mb-2">
                                            <Label text={isOpenModalType == "earnings" ?t("labels.earningType"):t("labels.deductionType")} required={true} />
                                            <CustomSelect
                                                data={isOpenModalType == "earnings" ?allEarningTypeData:allDeductionTypeData}
                                                placeholder={isOpenModalType == "earnings" ?t("placeholders.earningType"):t("placeholders.deductionType")}
                                                value={createUpdateEarningsFormData?.earningType || ''}
                                                handleChange={(selectedOption) => handleFromChange(selectedOption, "earningType")}
                                                name="earningType"
                                            />
                                            {errors?.earningType && (
                                                <CustomError text={errors.earningType} />
                                            )}
                                        </div>
                                    </div>

                                </div>
                                <div className="row">
                                    {/* <div className="col-md-6">
                                        <div className="form-group mb-2">
                                            <Label text={t("labels.name")} required={true} />
                                            <Input
                                                type="text"
                                                placeholder={t("placeholders.name")}
                                                value={createUpdateEarningsFormData?.earningName}
                                                name="earningName"
                                                onChange={(e) => handleFromChange(e)}
                                            />
                                        </div>
                                    </div> */}
                                    <div className="col-md-6">
                                        <div className="form-group mb-2">
                                            <Label text={t("labels.nameInPayslip")} required={true} />
                                            <Input
                                                type="text"
                                                placeholder={t("placeholders.nameInPayslip")}
                                                value={createUpdateEarningsFormData?.nameInPayslip}
                                                name="nameInPayslip"
                                                onChange={(e) => handleFromChange(e)}
                                            />

                                        </div>
                                    </div>
                                    <div className="col-md-12">
                                        <div className="form-group mb-2">
                                            <Label text={t("labels.calculationType")} required={true} />
                                            <CustomRadio
                                                data={ calculationTypeOptions}
                                                name="calculationType"
                                                handleChange={(e) => handleFromChange(e)}
                                                selectedValue={createUpdateEarningsFormData?.calculationType}
                                            />
                                        </div>
                                    </div>
                                    {createUpdateEarningsFormData?.calculationType !== "" &&
                                        createUpdateEarningsFormData?.calculationType !== "slab_wise"
                                        && <div className="col-md-6">
                                            <div className="form-group mb-2">
                                                <Label
                                                    text={checkCalculationType()?.label}
                                                />
                                                <Input
                                                    type="number"
                                                    placeholder={checkCalculationType()?.placeholder}
                                                    value={createUpdateEarningsFormData?.calculationTypeValue}
                                                    name="calculationTypeValue"
                                                    onChange={(e) => handleFromChange(e)}
                                                />
                                            </div>
                                        </div>}
                                    {/* <div className="col-md-12 row">
                                        <div className="form-group mb-2">
                                            <CustomStrong
                                                className={"text-primary"}
                                                text={t("labels.monthlySalary")}
                                                required={true}
                                            />
                                            <div className="col-md-12 mt-2">
                                                <div className="form-group mb-2 d-flex justify-content-between gap-3">
                                                    <span className='rupee-sign' >&#8377;</span>

                                                    <Input
                                                        type="number"
                                                        placeholder={t("placeholders.startSalary")}
                                                        value={createUpdateEarningsFormData?.startSalary}
                                                        name="startSalary"
                                                        onChange={(e) => handleFromChange(e)}
                                                    />
                                                    <span className='mt-2' style={{ fontSize: "16px" }} >To</span>
                                                    <Input
                                                        type="number"
                                                        placeholder={t("placeholders.endSalary")}
                                                        value={createUpdateEarningsFormData?.endSalary}
                                                        name="endSalary"
                                                        onChange={(e) => handleFromChange(e)}
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                    </div> */}

                                    <div className="col-md-12 row">
                                        <div className="form-group mb-2">
                                            <CustomStrong
                                                className={"text-primary"}
                                                text={t("labels.maxAllowance")}
                                                required={true}
                                            />
                                            <div className="col-md-6 mt-2">
                                                <div className="form-group mb-2 d-flex justify-content-between gap-3">
                                                    <span className='rupee-sign' >&#8377;</span>

                                                    <Input
                                                        type="number"
                                                        placeholder={t("placeholders.maxAllowance")}
                                                        value={createUpdateEarningsFormData?.maxAllowance}
                                                        name="maxAllowance"
                                                        onChange={(e) => handleFromChange(e)}
                                                    />

                                                </div>
                                            </div>
                                        </div>
                                    </div>


                                    <div className="col-md-12 d-flex">
                                        <div className="form-group ">
                                            <CustCheckbox
                                                key={"includedCTC"}
                                                id={`includedCTC`}
                                                checked={createUpdateEarningsFormData?.includedCTC}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"includedCTC"}
                                            />

                                        </div>
                                        <div className="form-group ">
                                            <CustomStrong text={t("labels.includedCTC")} className="text-primary" />
                                        </div>
                                    </div>
                                    <div className="col-md-12 d-flex">
                                        <div className="form-group ">
                                            <CustCheckbox
                                                key={"variablePay"}
                                                id={`variablePay`}
                                                checked={createUpdateEarningsFormData?.variablePay}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"variablePay"}
                                            />

                                        </div>
                                        <div className="form-group ">
                                            <CustomStrong text={t("labels.variablePay")} className="text-primary" />
                                        </div>
                                    </div>

                                    <div className="col-md-12 d-flex">
                                        <div className="form-group ">
                                            <CustCheckbox
                                                key={"taxableComp"}
                                                id={`taxableComp`}
                                                checked={createUpdateEarningsFormData?.taxableComp}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"taxableComp"}
                                            />

                                        </div>
                                        <div className="form-group ">
                                            <CustomStrong text={t("labels.taxableComp")} className="text-primary" />
                                            <p className='text-secondary'>{t("paragraph.taxableCompDesc")}</p>
                                        </div>
                                    </div>
                                    <div className="col-md-12 d-flex">
                                        <div className="form-group ">
                                            <CustCheckbox
                                                key={"isActive"}
                                                id={`isActive`}
                                                checked={createUpdateEarningsFormData?.isActive}
                                                handleChange={(e) => handleFromChange(e)}
                                                name={"isActive"}
                                            />

                                        </div>
                                        <div className="form-group">
                                            <CustomStrong text={t("labels.isActive")} className="text-primary" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="tab-pane fade" id={isOpenModalType == "earnings" ? "earning_rules" : "deduction_rules"}>
                            <EarningRules
                                allLeaveConstantData={allLeaveConstantData}
                                setRules={setRules}
                                rules={rules}
                            />
                        </div>
                        <div className="tab-pane fade" id="other_configurations">
                            {earningTypeByIdData &&earningTypeByIdData?.length> 0 &&  <ViewsEarningTypes
                                earningTypeData={earningTypeByIdData[0]}
                            />}
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={earningDeductionSelectedData ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateEarnings()}
                        // isLoading={earningDeductionSelectedData ? updateEarningsPending : addEarningsPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddUpdateEarnings
