import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { CustCheckbox, Input, Label } from '../../../commanComponent'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../../translation'
import { validateSchema } from '../../../../utils/ValidationHelper'
import { addUpdateInvestmentCategorySchema } from '../../../../utils/ValidationSchema'

const AddUpdateCategory = (props) => {
    const { t } = useCustomTranslation()
    // const { addInvestmentCategoryPending, updateInvestmentCategoryPending } = useSelector(state => state?.dropdownMaster)
    const {
        selectedInvestmentCategory,
        openModel,
        handleOpenClose,
        handleCreateUpdateInvestmentCategory,
        allDropdownConstantData,
    } = props

    const [errors, setErrors] = useState({})
    const [investmentCategoryFormData, setInvestmentCategoryFormData] = useState({
        category: null,
        alert:'',
        max_limit: false,
        max_limit_amount: 0,
        is_eligible_for_old: false,
        is_eligible_for_new: false,
        input_type:null
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setInvestmentCategoryFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitCreateUpdateCategory = async () => {
        const validationResponse = await validateSchema(addUpdateInvestmentCategorySchema, investmentCategoryFormData);
        if (validationResponse.isValid) {
            const {
                category,
                max_limit,
                alert,
                max_limit_amount,
                is_eligible_for_old,
                is_eligible_for_new,
                input_type
            } = investmentCategoryFormData
            const params = {
                category,
                alert,
                max_limit,
                max_limit_amount,
                is_eligible_for_old,
                is_eligible_for_new,
                input_type:input_type?.value

            }
            console.log(params, "params")
            handleCreateUpdateInvestmentCategory(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedInvestmentCategory !== null) {
            setInvestmentCategoryFormData({
                category: selectedInvestmentCategory?.category,
                max_limit: selectedInvestmentCategory?.max_limit,
                alert:selectedInvestmentCategory?.alert,
                max_limit_amount: selectedInvestmentCategory?.max_limit_amount,
                is_eligible_for_old: selectedInvestmentCategory?.is_eligible_for_old,
                is_eligible_for_new: selectedInvestmentCategory?.is_eligible_for_new,
                input_type:{label:selectedInvestmentCategory?.input_type,value:selectedInvestmentCategory?.input_type}

            })
        }
    }, [selectedInvestmentCategory])
    const resetForm = () => {
        setInvestmentCategoryFormData({
            category: null,
            alert:'',
            input_type:null,
            max_limit: '',
            max_limit_amount: '',
            is_eligible_for_old: '',
            is_eligible_for_new: null,
        })
    }
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedInvestmentCategory ? t("headings.updateInvestmentCategory") : t("headings.addInvestmentCategory")} </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("category")}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.category")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.categoryName")}
                                        value={investmentCategoryFormData?.category}
                                        name="category"
                                        onChange={(e) => handleFromChange(e)}
                                    />

                                    {errors?.category && (
                                        <CustomError text={errors.category} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.alert")}  />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.alert")}
                                        value={investmentCategoryFormData?.alert}
                                        name="alert"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.alert && (
                                        <CustomError text={errors.alert} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3"> 
                                    <Label text={t("labels.inputType")}/>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDropdownConstantData?.dropdownInputType}
                                        placeholder={t("placeholders.inputType")}
                                        value={investmentCategoryFormData?.input_type || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "input_type")}
                                        name="input_type"
                                    />
                                    {errors?.input_type && (
                                        <CustomError text={errors.input_type} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.max_limit")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"max_limit"}
                                        id={`max_limit`}
                                        checked={investmentCategoryFormData?.max_limit}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"max_limit"}
                                    />
                                </div>
                            </div>
                        </div>
                        {investmentCategoryFormData?.max_limit && <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.max_limit_amount")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.max_limit_amount")}
                                        value={investmentCategoryFormData?.max_limit_amount}
                                        name="max_limit_amount"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.max_limit_amount && (
                                        <CustomError text={errors.max_limit_amount} />
                                    )}
                                </div>
                            </div>
                        </div>}
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.is_eligible_for_new")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_eligible_for_new"}
                                        id={`is_eligible_for_new`}
                                        checked={investmentCategoryFormData?.is_eligible_for_new}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_eligible_for_new"}
                                    />

                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.is_eligible_for_old")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_eligible_for_old"}
                                        id={`is_eligible_for_old`}
                                        checked={investmentCategoryFormData?.is_eligible_for_old}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_eligible_for_old"}
                                    />

                                </div>
                            </div>
                        </div>
                        {/* <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        checked={investmentCategoryFormData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div> */}

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedInvestmentCategory !== null ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitCreateUpdateCategory()}
                        // isLoading={selectedInvestmentCategory ? updateInvestmentCategoryPending : addInvestmentCategoryPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddUpdateCategory
