
import React, { useEffect, useState } from 'react'
import { CustCheckbox, CustomRadio, CustomStrong, Icon, Input, Label } from '../../../commanComponent'
import { CustomButton, CustomError } from '../../../globalComponents'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../../translation'
import { deductionFrequency } from '../../../../utils/Constants.config'

const AddPreTaxDeduction = (props) => {
    const { t } = useCustomTranslation()
    const {
        openModel,
        handleOpenClose,
        deductionWithData,
        deductionPlanData
    } = props
    const [errors, setErrors] = useState({})
    const [preTaxDeductionFormData, setPreTaxDeductionFormData] = useState({

        nameInPayslip: '',
        deductionFrequency: '',
        is_active: true
    })


    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setPreTaxDeductionFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Clear error for the specific field being changed
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitPreTaxDeduction = async () => {

    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])



    const resetForm = () => {
        setPreTaxDeductionFormData({
            nameInPayslip: '',
            deductionFrequency: '',
            is_active: true
        })
        setErrors({})
    }

    return (
        <>
        <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "600px"
            }}
            id="interviewDetail"
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Pre Tax Deduction</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={
                        () => handleOpenClose("pre_tax_deduction")
                    }
                />
            </div>

            <div className="offcanvas-body myRequest ">
                <div className="formPanel">
                    <div className="row">
                        <div className="col-md-12" >
                            <div className="form-group mb-2">
                                <Label text={t("labels.deductionPlan")} required={true} />
                                <CustomSelect
                                    data={deductionPlanData}
                                    placeholder={t("placeholders.deductionPlan")}
                                    value={preTaxDeductionFormData?.deductionPlan || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "deductionPlan")}
                                    name="deductionPlan"
                                />

                            </div>
                        </div>
                        <div className="col-md-12" >
                            <div className="form-group mb-2">
                                <Label text={t("labels.deductionWith")} required={true} />
                                <CustomSelect
                                    data={deductionWithData}
                                    placeholder={t("placeholders.deductionWith")}
                                    value={preTaxDeductionFormData?.deductionWith || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "deductionWith")}
                                    name="deductionWith"
                                />

                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="form-group mb-2">
                                <Label text={t("labels.nameInPayslip")} required={true} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.nameInPayslip")}
                                    value={preTaxDeductionFormData?.nameInPayslip}
                                    name="nameInPayslip"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.nameInPayslip && (
                                    <CustomError text={errors.nameInPayslip} />
                                )}
                            </div>
                        </div>

                        <div className="col-md-12 mt-3 d-flex">
                            <div className="form-group">
                                <CustCheckbox
                                    key={"employerContributionCTC"}
                                    id={`employerContributionCTC`}
                                    checked={preTaxDeductionFormData?.employerContributionCTC}
                                    handleChange={(e) => handleFromChange(e)}
                                    name={"employerContributionCTC"}
                                />

                            </div>
                            <div className="form-group ">
                                <CustomStrong text={t("labels.employerContributionCTC")} className="text-primary"/>
                            </div>

                        </div>
                        <div className="col-md-12 mt-3 d-flex">
                            <div className="form-group">
                                <CustCheckbox
                                    key={"calculateProRataBasis"}
                                    id={`calculateProRataBasis`}
                                    checked={preTaxDeductionFormData?.calculateProRataBasis}
                                    handleChange={(e) => handleFromChange(e)}
                                    name={"calculateProRataBasis"}
                                />

                            </div>
                            <div className="form-group ">
                                <CustomStrong text={t("labels.calculateProRataBasis")} className="text-primary"/>
                                <p className='text-secondary'>Pay will be adjusted based on employee working days.</p>
                            </div>

                        </div>

                        <div className="col-md-12 d-flex">
                            <div className="form-group mb-3">
                                <CustCheckbox
                                    key={"is_active"}
                                    id={`is_active`}
                                    checked={preTaxDeductionFormData?.is_active}
                                    handleChange={(e) => handleFromChange(e)}
                                    name={"is_active"}
                                />

                            </div>
                            <div className="form-group mb-3">
                                <CustomStrong text={t("labels.isActive")}  className="text-primary"/>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">
                    <CustomButton
                        text={t("btns.submit")}
                        classNames={"btn btn-org-gradient"}
                        handleClick={() => submitPreTaxDeduction()}
                    />
                </div>
            </div>
        </div>
        {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddPreTaxDeduction
