import React, { useEffect, useState } from 'react'
import CustomCards from '../../reuseableComponents/CustomCards'
import SalaryComponent from './salaryComponent/SalaryComponent'
import AddUpdateEarnings from './salaryComponent/AddUpdateEarnings'
import PaySchedule from './paySchedule/PaySchedule'
import AddPostTaxDeduction from './salaryComponent/AddPostTaxDeduction'
import AddPreTaxDeduction from './salaryComponent/AddPreTaxDeduction'
import { useDispatch, useSelector } from 'react-redux'
import { commonActions, dropdownMasterActions, leaveAction, payrollActions } from '../../../core/resource/actions'
import AddUpdatePaySchedule from './paySchedule/AddUpdatePaySchedule'
import TaxSlab from './taxSlab/TaxSlab'
import AddUpdateTaxSlab from './taxSlab/AddUpdateTaxSlab'
import { pagination } from '../../../utils/Constants.config'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'
import Investments from './itDeclaration/Investments'
import AddUpdateSubCategory from './itDeclaration/AddUpdateSubCategory'
import AddUpdateCategory from './itDeclaration/AddUpdateCategory'

const PayrollSetting = () => {
  const [activeCard, setActiveCard] = useState('salary_component')
  const { t } = useCustomTranslation()
  const dispatch = useDispatch()
  const {
    addEarningsPending,
    addEarningsRes,
    addEarningsError,

    updateEarningsPending,
    updateEarningsRes,
    updateEarningsError,

    getEarningsPending,
    getEarningsRes,
    getEarningsError,

    addDeductionsPending,
    addDeductionsRes,
    addDeductionsError,

    updateDeductionsPending,
    updateDeductionsRes,
    updateDeductionsError,

    getDeductionsPending,
    getDeductionsRes,
    getDeductionsError,

    getAllEarningTypePending,
    getAllEarningTypeRes,
    getAllEarningTypeError,

    getAllDeductionTypePending,
    getAllDeductionTypeRes,
    getAllDeductionTypeError,

    getEarningTypeByIdPending,
    getEarningTypeByIdRes,
    getEarningTypeByIdError,

    addPaySchedulePending,
    addPayScheduleRes,
    addPayScheduleError,

    updatePaySchedulePending,
    updatePayScheduleRes,
    updatePayScheduleError,

    upcomingPayrollsPending,
    upcomingPayrollsRes,
    upcomingPayrollsError,

    addTaxSlabPending,
    addTaxSlabRes,
    addTaxSlabError,

    updateTaxSlabPending,
    updateTaxSlabRes,
    updateTaxSlabError,

    getTaxSlabPending,
    getTaxSlabRes,
    getTaxSlabError,

    deleteTaxSlabPending,
    deleteTaxSlabRes,
    deleteTaxSlabError,

    addInvestmentCategoryPending,
    addInvestmentCategoryRes,
    addInvestmentCategoryError,

    updateInvestmentCategoryPending,
    updateInvestmentCategoryRes,
    updateInvestmentCategoryError,

    getInvestmentCategoryPending,
    getInvestmentCategoryRes,
    getInvestmentCategoryError,

    getAllInvestmentCategoryPending,
    getAllInvestmentCategoryRes,
    getAllInvestmentCategoryError,

    deleteInvestmentCategoryPending,
    deleteInvestmentCategoryRes,
    deleteInvestmentCategoryError,

    addInvestmentSubCategoryPending,
    addInvestmentSubCategoryRes,
    addInvestmentSubCategoryError,

    updateInvestmentSubCategoryPending,
    updateInvestmentSubCategoryRes,
    updateInvestmentSubCategoryError,

    getInvestmentSubCategoryPending,
    getInvestmentSubCategoryRes,
    getInvestmentSubCategoryError,

    getAllInvestmentSubCategoryPending,
    getAllInvestmentSubCategoryRes,
    getAllInvestmentSubCategoryError,

    deleteInvestmentSubCategoryPending,
    deleteInvestmentSubCategoryRes,
    deleteInvestmentSubCategoryError,

  } = useSelector(state => state.payroll)

  const {
    getDropdownMasterConstantDataPending,
    getDropdownMasterConstantDataRes,
    getDropdownMasterConstantDataError,

  } = useSelector(state => state.dropdownMaster)

  const {
    getLeaveConstantDataPending,
    getLeaveConstantDataRes,
    getLeaveConstantDataError,
  } = useSelector(state => state.leave)

  const {
    getAppConstantDataPending,
    getAppConstantDataRes,
    getAppConstantDataError,
  } = useSelector(state => state.common)
  const {
    getFilteredDropdownDataError,
    getFilteredDropdownDataRes,
    getFilteredDropdownDataPending
  } = useSelector(state => state.dropdownMaster)

  const [isOpenAddUpEarningModal, setIsOpenAddUpEarningModal] = useState(false)
  const [isOpenAddPostTaxDeductionModal, setIsOpenAddPostTaxDeductionModal] = useState(false)
  const [isOpenAddPreTaxDeductionModal, setIsOpenAddPreTaxDeductionModal] = useState(false)
  const [currentPage, setCurrentPage] = useState({
    taxSlabCurrPage: 1,
    investCategoryCurrPage: 1,
    investmentSubCategoryCurrPage:1
  })
  const [allDeductionTypeData, setAllDeductionTypeData] = useState('')
  const [allEarningTypeData, setAllEarningTypeData] = useState('')
  const [earningTypeByIdData, setEarningTypeByIdData] = useState('')
  const [allLeaveConstantData, setAllLeaveConstantData] = useState('')
  const [earningData, setEarningData] = useState('')
  const [isOpenModalType, setIsOpenModalType] = useState('earnings')
  const [deductionsData, setDeductionsData] = useState('earnings')
  const [earningDeductionSelectedData, setEarningDeductionSelectedData] = useState('')
  const [appConstantData, setAppConstantData] = useState('')
  const [upcomingPayrollData, setUpcomingPayrollData] = useState('')
  const [isOpenPayScheduleModal, setIsOpenPayScheduleModal] = useState(false)
  const [isOpenTaxSlabModal, setIsOpenTaxSlabModal] = useState(false)
  const [taxSlabData, setTaxSlabData] = useState('')
  const [selectedPayScheduleData, setSelectedPayScheduleData] = useState('')
  const [selectedTaxSlabData, setSelectedTaxSlabData] = useState('')
  const [filteredDropdownData, setFilteredDropdownData] = useState({})
  const [deleteTaxSlabId, setDeleteTaxSlabId] = useState('')
  const [isShowDeleteTaxSlabModal, setIsShowDeleteTaxSlabModal] = useState(false)
  const [isShowDeleteInvestmentModal, setIsShowDeleteInvestmentModal] = useState(false)
  const [investmentCategoryListData, setInvestmentCategoryListData] = useState([])
  const [openModals, setOpenModals] = useState({
    isOpenCategoryModal: false,
    isOpenSubCategoryModal: false
  })
  const [selectedInvestmentData, setSelectedInvestmentData] = useState({
    categoryData: null,
    subCategoryData: null
  })
  const [investmentCategoryData, setInvestmentCategoryData] = useState('')
  const [investmentSubCategoryData, setInvestmentSubCategoryData] = useState('')
  const [investmentSubCategoryList, setInvestmentSubCategoryList] = useState('')
  const [allDropdownConstantData,setAllDropdownConstantData]=useState('')


  const payrollSettingCard = [
    {
      name: "Salary Component",
      imgUrl: "images/applyleavesIcon.svg",
      onClickFunc: (id) => handleActiveTab(id),
      id: "salary_component"
    },
    {
      name: "Pay Schedule",
      imgUrl: "images/bonus-icon.svg",
      onClickFunc: (id) => handleActiveTab(id),
      id: "pay_schedule"
    },
    {
      name: "IT Declaration",
      imgUrl: "images/attendancelogIcon.svg",
      onClickFunc: (id) => handleActiveTab(id),
      id: "it_declaration"
    },
    {
      name: "Tax Slab",
      imgUrl: "images/attendancelogIcon.svg",
      onClickFunc: (id) => handleActiveTab(id),
      id: "tax_slab"
    },
  ]

  /* --------------Handle Tab Change in Setting------------- */

  const handleActiveTab = (type) => {
    if (type === "pay_schedule") {
      upcomingPayrollsData()
    } else if (type === "tax_slab") {
      handleTaxSlabData()
    } else if (type === "it_declaration") {
      getInvestmentCategoryData()
      getInvestmentSubCategoryData()
    }
    setActiveCard(type)
    setCurrentPage({
      taxSlabCurrPage: 1,
      investCategoryCurrPage: 1
    })
  }

  const handleToggleModals = (type, data) => {
    if (type === "add_earnings") {
      if (data) {
        setEarningDeductionSelectedData(data)
      } else {
        setEarningDeductionSelectedData('')
      }
      setIsOpenAddUpEarningModal(!isOpenAddUpEarningModal)
      setIsOpenModalType("earnings")
    }
    else if (type === "deduction") {
      if (data) {
        setEarningDeductionSelectedData(data)
      }
      else {
        setEarningDeductionSelectedData('')
      }
      setIsOpenAddUpEarningModal(!isOpenAddUpEarningModal)
      setIsOpenModalType("deduction")
    }
    else if (type === "post_tax_deduction") {
      setIsOpenAddPostTaxDeductionModal(!isOpenAddPostTaxDeductionModal)
    }
    else if (type === "pre_tax_deduction") {
      setIsOpenAddPreTaxDeductionModal(!isOpenAddPreTaxDeductionModal)
    }
    else if (type === "pay_schedule") {
      if (data) {
        setSelectedPayScheduleData(data)
      }
      else {
        setSelectedPayScheduleData('')
      }
      setIsOpenPayScheduleModal(!isOpenPayScheduleModal)
    }
    else if (type === "tax_slab") {
      if (data) {
        setSelectedTaxSlabData(data)
      }
      else {
        setSelectedTaxSlabData('')
      }
      setIsOpenTaxSlabModal(!isOpenTaxSlabModal)
    }
  }

  /*----------- Handle Page change (Pagination of all) ------------- */

  const handlePageChange = (pageNo, type) => {
    if (type === "tax_slab") {
      setCurrentPage(pre => ({
        ...pre,
        taxSlabCurrPage: pageNo
      }))
      handleTaxSlabData(pageNo)
    }
   
  }

  const handlePageChangeITDeclaration = (pageNo, type) => {
    if (type === "category") {
      setCurrentPage(pre => ({
        ...pre,
        investCategoryCurrPage: pageNo
      }))
      getInvestmentCategoryData(pageNo)
    }else if(type ==="sub_category"){
      setCurrentPage(pre => ({
        ...pre,
        investmentSubCategoryCurrPage: pageNo
      }))
      getInvestmentSubCategoryData(pageNo)
    }
   
  }





  /* Add Earning  */
  const addEarnings = (payload) => {
    dispatch(payrollActions.addEarningsRequest(payload))
  }
  useEffect(() => {
    if (!addEarningsPending) {
      if (addEarningsError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_EARNING_STORE"))
        return;
      }
      if (addEarningsRes) {
        if (addEarningsRes?.code === 200) {
          getEarnings()
          handleToggleModals("add_earnings")
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_EARNING_STORE"))

        }
      }
    }
  }, [
    addEarningsRes,
    addEarningsError,
    addEarningsPending
  ]);

  /* Update Earning  */
  const updateEarnings = (payload) => {
    dispatch(payrollActions.updateEarningsRequest(payload))
  }
  useEffect(() => {
    if (!updateEarningsPending) {
      if (updateEarningsError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_EARNING_STORE"))
        return;
      }
      if (updateEarningsRes) {
        if (updateEarningsRes?.code === 200) {
          getEarnings()
          setEarningDeductionSelectedData('')
          handleToggleModals("add_earnings")
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_EARNING_STORE"))

        }
      }
    }
  }, [
    updateEarningsRes,
    updateEarningsError,
    updateEarningsPending
  ]);




  /* update Deduction Types */
  const updateDeductions = (payload) => {
    dispatch(payrollActions.updateDeductionsRequest(payload))
  }
  useEffect(() => {
    if (!updateDeductionsPending) {
      if (updateDeductionsError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_DEDUCTIONS_STORE"))
        return;
      }
      if (updateDeductionsRes) {
        if (updateDeductionsRes?.code === 200) {
          handleToggleModals("deduction")
          setEarningDeductionSelectedData('')
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_DEDUCTIONS_STORE"))

        }
      }
    }
  }, [
    updateDeductionsRes,
    updateDeductionsError,
    updateDeductionsPending
  ]);

  /* Add Deduction Types */
  const addDeductions = (payload) => {
    dispatch(payrollActions.addDeductionsRequest(payload))
  }
  useEffect(() => {
    if (!addDeductionsPending) {
      if (addDeductionsError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_DEDUCTIONS_STORE"))
        return;
      }
      if (addDeductionsRes) {
        if (addDeductionsRes?.code === 200) {
          handleToggleModals("deduction")
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_DEDUCTIONS_STORE"))

        }
      }
    }
  }, [
    addDeductionsRes,
    addDeductionsError,
    addDeductionsPending
  ]);

  useEffect(() => {

    getLeaveConstantData()
    getAllEarningType()
    getEarnings()
    getDeductions()
    getAllDeductionType()
    getDropdownFilteredData()
    getDropdownMasterConstantData()
  }, [])


  /* Get Leave Constant Data */
  const getLeaveConstantData = (params) => {
    dispatch(leaveAction.getLeaveConstantDataRequest(params));
  }
  useEffect(() => {
    if (!getLeaveConstantDataPending) {
      if (getLeaveConstantDataError) {
        return;
      }
      if (getLeaveConstantDataRes) {
        if (getLeaveConstantDataRes?.code === 200) {
          const getLeaveConstantData = getLeaveConstantDataRes?.data
          setAllLeaveConstantData(getLeaveConstantData)
        }
      }
    }
  }, [
    getLeaveConstantDataRes,
    getLeaveConstantDataError,
    getLeaveConstantDataPending
  ]);

  /* Get All Earning Type Data */
  const getAllEarningType = () => {
    dispatch(payrollActions.getAllEarningTypeRequest());
  }
  useEffect(() => {
    if (!getAllEarningTypePending) {
      if (getAllEarningTypeError) {
        return;
      }
      if (getAllEarningTypeRes) {
        if (getAllEarningTypeRes?.code === 200) {
          const getAllEarningTypeResData = getAllEarningTypeRes?.data
          setAllEarningTypeData(getAllEarningTypeResData)
        }
      }
    }
  }, [
    getAllEarningTypeRes,
    getAllEarningTypeError,
    getAllEarningTypePending
  ]);

  /* Get All Deduction Type Data */
  const getAllDeductionType = () => {
    dispatch(payrollActions.getAllDeductionTypeRequest());
  }
  useEffect(() => {
    if (!getAllDeductionTypePending) {
      if (getAllDeductionTypeError) {
        return;
      }
      if (getAllDeductionTypeRes) {
        if (getAllDeductionTypeRes?.code === 200) {
          const getAllDeductionTypeResData = getAllDeductionTypeRes?.data
          setAllDeductionTypeData(getAllDeductionTypeResData)
        }
      }
    }
  }, [
    getAllDeductionTypeRes,
    getAllDeductionTypeError,
    getAllDeductionTypePending
  ]);


  /* Get All Earning Type Data */
  const getEarningTypeById = (id) => {
    dispatch(payrollActions.getEarningTypeByIdRequest({
      id
    }));
  }
  useEffect(() => {
    if (!getEarningTypeByIdPending) {
      if (getEarningTypeByIdError) {
        return;
      }
      if (getEarningTypeByIdRes) {
        if (getEarningTypeByIdRes?.code === 200) {
          const getEarningTypeByIdResData = getEarningTypeByIdRes?.data
          setEarningTypeByIdData(getEarningTypeByIdResData)
        }
      }
    }
  }, [
    getAllEarningTypeRes,
    getEarningTypeByIdError,
    getEarningTypeByIdPending
  ]);


  /* Get All Earning Type Data */
  const getEarnings = (id) => {
    dispatch(payrollActions.getEarningsRequest());
  }
  useEffect(() => {
    if (!getEarningsPending) {
      if (getEarningsError) {
        return;
      }
      if (getEarningsRes) {
        if (getEarningsRes?.code === 200) {
          const getEarningsResData = getEarningsRes?.data
          setEarningData(getEarningsResData)
        }
      }
    }
  }, [
    getAllEarningTypeRes,
    getEarningsError,
    getEarningsPending
  ]);


  /* Get Deductions Data */
  const getDeductions = (id) => {
    dispatch(payrollActions.getDeductionsRequest());
  }
  useEffect(() => {
    if (!getDeductionsPending) {
      if (getDeductionsError) {
        return;
      }
      if (getDeductionsRes) {
        if (getDeductionsRes?.code === 200) {
          const getDeductionsResData = getDeductionsRes?.data
          setDeductionsData(getDeductionsResData)
        }
      }
    }
  }, [
    getDeductionsRes,
    getDeductionsError,
    getDeductionsPending
  ]);

  /*Get App Constant Data */
  useEffect(() => {
    getAppConstantData()
    getInvestmentCategory()
  }, [])
  const getAppConstantData = () => {
    dispatch(commonActions.getAppConstantDataRequest())
  }
  useEffect(() => {
    if (!getAppConstantDataPending) {
      if (getAppConstantDataError) {
        return;
      }
      if (getAppConstantDataRes) {
        if (getAppConstantDataRes?.code === 200) {
          setAppConstantData(getAppConstantDataRes?.data)
        }
      }
    }
  }, [
    getAppConstantDataRes,
    getAppConstantDataError,
    getAppConstantDataPending
  ]);

  /* Add Pay Schedule  */

  const handleAddPayScheduleData = (payload) => {
    dispatch(payrollActions.addPayScheduleRequest(payload))
  }
  useEffect(() => {
    if (!addPaySchedulePending) {
      if (addPayScheduleError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_PAY_SCHEDULE_STORE"))
        return;
      }
      if (addPayScheduleRes) {
        if (addPayScheduleRes?.code === 200) {
          handleToggleModals("pay_schedule")
          upcomingPayrollsData()
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_PAY_SCHEDULE_STORE"))
        }
      }
    }
  }, [
    addPayScheduleRes,
    addPayScheduleError,
    addPaySchedulePending
  ]);

  /* Update Pay Schedule  */
  const handleUpdatePaySchedule = (payload) => {
    dispatch(payrollActions.updatePayScheduleRequest(payload))
  }
  useEffect(() => {
    if (!updatePaySchedulePending) {
      if (updatePayScheduleError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_PAY_SCHEDULE_STORE"))
        return;
      }
      if (updatePayScheduleRes) {
        if (updatePayScheduleRes?.code === 200) {
          handleToggleModals("pay_schedule")
          upcomingPayrollsData()
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_PAY_SCHEDULE_STORE"))
        }
      }
    }
  }, [
    updatePayScheduleRes,
    updatePayScheduleError,
    updatePaySchedulePending
  ]);


  /* Update Pay Schedule  */
  const upcomingPayrollsData = () => {
    dispatch(payrollActions.upcomingPayrollsRequest())
  }
  useEffect(() => {
    if (!upcomingPayrollsPending) {
      if (upcomingPayrollsError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPCOMING_PAY_ROLL_STORE"))
        return;
      }
      if (upcomingPayrollsRes) {
        if (upcomingPayrollsRes?.code === 200) {
          setUpcomingPayrollData(upcomingPayrollsRes?.data)
        }
      }
    }
  }, [
    upcomingPayrollsRes,
    upcomingPayrollsError,
    upcomingPayrollsPending
  ]);

  /* Add Tax Slab   */
  const handleAddUpdateTaxSlab = (payload) => {
    if (selectedTaxSlabData) {
      dispatch(payrollActions.updateTaxSlabRequest(payload))
    } else {
      dispatch(payrollActions.addTaxSlabRequest(payload))
    }
  }
  useEffect(() => {
    if (!addTaxSlabPending) {
      if (addTaxSlabError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_TAX_SLAB_STORE"))
        return;
      }
      if (addTaxSlabRes) {
        if (addTaxSlabRes?.code === 200) {
          // setUpcomingPayrollData(addTaxSlabRes?.data)
          handleToggleModals("tax_slab")
          handleTaxSlabData()
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_TAX_SLAB_STORE"))
        }
      }
    }
  }, [
    addTaxSlabRes,
    addTaxSlabError,
    addTaxSlabPending
  ]);

  /* Add Tax Slab   */

  useEffect(() => {
    if (!updateTaxSlabPending) {
      if (updateTaxSlabError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_TAX_SLAB_STORE"))
        return;
      }
      if (updateTaxSlabRes) {
        if (updateTaxSlabRes?.code === 200) {
          handleToggleModals("tax_slab")
          handleTaxSlabData()
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_TAX_SLAB_STORE"))
        }
      }
    }
  }, [
    updateTaxSlabRes,
    updateTaxSlabError,
    updateTaxSlabPending
  ]);

  /* Get Tax Slab Data   */
  const handleTaxSlabData = (pageNo) => {
    const payload = {
      page: pageNo || currentPage?.taxSlabCurrPage,
      limit: pagination?.limit

    }
    dispatch(payrollActions.getTaxSlabRequest(payload))
  }
  useEffect(() => {
    if (!getTaxSlabPending) {
      if (getTaxSlabError) {
        return;
      }
      if (getTaxSlabRes) {
        if (getTaxSlabRes?.code === 200) {
          setTaxSlabData(getTaxSlabRes?.data)
        }
      }
    }
  }, [
    getTaxSlabRes,
    getTaxSlabError,
    getTaxSlabPending
  ]);

  //dropdown master filter 
  const getDropdownFilteredData = () => {
    const category = ['tax_slab']
    let params = { category: '' }
    category.forEach((item) => {
      params.category = item
      dispatch(dropdownMasterActions.getFilteredDropdownDataRequest(params))
    });
  }

  useEffect(() => {
    if (!getFilteredDropdownDataPending) {
      if (getFilteredDropdownDataError) {
        return;
      }
      if (getFilteredDropdownDataRes) {
        if (getFilteredDropdownDataRes?.code === 200) {
          const dropdownFilterData = getFilteredDropdownDataRes?.data
          let categoryName = dropdownFilterData?.category
          setFilteredDropdownData(prevState => ({
            ...prevState,
            [categoryName]: dropdownFilterData?.data || []
          }));
        }
      }
    }
  }, [
    getFilteredDropdownDataRes,
    getFilteredDropdownDataError,
    getFilteredDropdownDataPending
  ]);


  /* Delete Tax Slab  */
  const onDeleteCancelTaxSlab = (type) => {
    if (type) {
      const params = {
        _id: deleteTaxSlabId
      }
      dispatch(payrollActions?.deleteTaxSlabRequest(params))
    } else {
      toggleDeleteTaxSlabModal()
    }
  }
  const toggleDeleteTaxSlabModal = (id) => {
    if (id) {
      setDeleteTaxSlabId(id)
    }
    setIsShowDeleteTaxSlabModal(!isShowDeleteTaxSlabModal)
  }

  useEffect(() => {
    if (!deleteTaxSlabPending) {
      if (deleteTaxSlabError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_DELETE_TAX_SLAB_STORE"))
        return;
      }
      if (deleteTaxSlabRes) {
        if (deleteTaxSlabRes?.code === 200) {
          toggleDeleteTaxSlabModal()
          handleTaxSlabData()
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_DELETE_TAX_SLAB_STORE"))
        }
      }
    }
  }, [
    deleteTaxSlabRes,
    deleteTaxSlabError,
    deleteTaxSlabPending
  ]);

  /* -------------------It Declaration Section------------------- */


  const handleOpenCloseModals = (modalType, data) => {
    if (modalType === "category") {
      if (data) {
        setSelectedInvestmentData(pre => ({
          ...pre,
          categoryData: data
        }))
      } else {
        setSelectedInvestmentData(pre => ({
          ...pre,
          categoryData: null
        }))
      }
      setOpenModals(pre => ({
        ...pre,
        isOpenCategoryModal: !pre?.isOpenCategoryModal
      }))
    } else if (modalType === "sub_category") {

      if (data) {
        setSelectedInvestmentData(pre => ({
          ...pre,
          subCategoryData: data
        }))
      } else {
        setSelectedInvestmentData(pre => ({
          ...pre,
          subCategoryData: null
        }))
      }

      setOpenModals(pre => ({
        ...pre,
        isOpenSubCategoryModal: !pre?.isOpenSubCategoryModal
      }))
    }

  }
  const toggleDeleteInvestmentModal = (modalType, data) => {
    if (modalType === "category") {
      if (data) {
        setSelectedInvestmentData(pre => ({
          ...pre,
          categoryData: data
        }))
      } else {
        setSelectedInvestmentData(pre => ({
          ...pre,
          categoryData: null
        }))
      }
    }
    else if(modalType === "sub_category"){
      if (data) {
        setSelectedInvestmentData(pre => ({
          ...pre,
          subCategoryData: data
        }))
      } else {
        setSelectedInvestmentData(pre => ({
          ...pre,
          subCategoryData: null
        }))
      }
    }
    setIsShowDeleteInvestmentModal(!isShowDeleteInvestmentModal)
  }
  const onDeleteCancelInvestmentCatSub = (type) => {
    if (type) {
     
      if (selectedInvestmentData?.categoryData) {
        const params = {
          _id: selectedInvestmentData?.categoryData?._id
        }
        dispatch(payrollActions?.deleteInvestmentCategoryRequest(params))
      }
      if (selectedInvestmentData?.subCategoryData) {
        const params = {
          _id: selectedInvestmentData?.subCategoryData?._id
        }
        dispatch(payrollActions?.deleteInvestmentSubCategoryRequest(params))
      }
    } else {
      toggleDeleteInvestmentModal()
    }
  }
  /* ----------------Investment Category------------*/

  const clearInvestmentStoreData = (type) => {
    if(type ==="category"){
      dispatch(payrollActions.handleClearPayrollStore("HANDLE_GET_INVESTMENT_CATEGORY_STORE"))
      dispatch(payrollActions.handleClearPayrollStore("HANDLE_GET_ALL_INVESTMENT_CATEGORY_STORE"))
    }else if(type ==="sub_category"){
      dispatch(payrollActions.handleClearPayrollStore("HANDLE_GET_INVESTMENT_SUB_CATEGORY_STORE"))
      dispatch(payrollActions.handleClearPayrollStore("HANDLE_GET_ALL_INVESTMENT_SUB_CATEGORY_STORE"))
    }
  }


  /* Create Update Investment Category */
  const handleCreateUpdateInvestmentCategory = (params) => {
    if (selectedInvestmentData?.categoryData) {
      params['_id'] = selectedInvestmentData?.categoryData?._id || selectedInvestmentData?.categoryData?.id
      dispatch(payrollActions.updateInvestmentCategoryRequest(params));
    }
    else {
      dispatch(payrollActions.addInvestmentCategoryRequest(params));
    }
  }
  useEffect(() => {
    if (!addInvestmentCategoryPending) {
      if (addInvestmentCategoryError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_ADD_INVESTMENT_CATEGORY_STORE"))
        clearInvestmentStoreData("category")
        return;
      }
      if (addInvestmentCategoryRes) {
        if (addInvestmentCategoryRes?.code === 200) {
          handleOpenCloseModals("category")
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_ADD_INVESTMENT_CATEGORY_STORE"))
          clearInvestmentStoreData("category")
          getInvestmentCategoryData()
          getInvestmentCategory()
          
        }
      }
    }
  }, [
    addInvestmentCategoryPending,
    addInvestmentCategoryRes,
    addInvestmentCategoryError,
  ]);
  useEffect(() => {
    if (!updateInvestmentCategoryPending) {
      if (updateInvestmentCategoryError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_UPDATE_INVESTMENT_CATEGORY_STORE"))
        clearInvestmentStoreData("category")
        return;
      }
      if (updateInvestmentCategoryRes) {
        if (updateInvestmentCategoryRes?.code === 200) {
          handleOpenCloseModals("category")
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_UPDATE_INVESTMENT_CATEGORY_STORE"))
          clearInvestmentStoreData("category")
          getInvestmentCategoryData()
          getInvestmentCategory() 
        }
      }
    }
  }, [
    updateInvestmentCategoryPending,
    updateInvestmentCategoryRes,
    updateInvestmentCategoryError,
  ]);

  useEffect(() => {
    if (!deleteInvestmentCategoryPending) {
      if (deleteInvestmentCategoryError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_DELETE_INVESTMENT_CATEGORY_STORE"))
        clearInvestmentStoreData("category")
        return;
      }
      if (deleteInvestmentCategoryRes) {
        if (deleteInvestmentCategoryRes?.code === 200) {
          toggleDeleteInvestmentModal("category")
          clearInvestmentStoreData("category")
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_DELETE_INVESTMENT_CATEGORY_STORE"))
          getInvestmentCategoryData()
          getInvestmentCategory()
        }
      }
    }
  }, [
    deleteInvestmentCategoryPending,
    deleteInvestmentCategoryRes,
    deleteInvestmentCategoryError,
  ]);

  /*Get All Investment Category */
  const getInvestmentCategoryData = (pageNo) => {
    const payload = {
      page: pageNo || currentPage?.investCategoryCurrPage,
      limit: pagination?.limit

    }
    dispatch(payrollActions.getAllInvestmentCategoryRequest(payload))
  }
  useEffect(() => {
    if (!getAllInvestmentCategoryPending) {
      if (getAllInvestmentCategoryError) {

        return;
      }
      if (getAllInvestmentCategoryRes) {
        if (getAllInvestmentCategoryRes?.code === 200) {
          setInvestmentCategoryData(getAllInvestmentCategoryRes?.data)
        }
      }
    }
  }, [
    getAllInvestmentCategoryPending,
    getAllInvestmentCategoryRes,
    getAllInvestmentCategoryError,
  ]);

  /* Get Investment Categories Data */
  const getInvestmentCategory = () => {
    dispatch(payrollActions.getInvestmentCategoryRequest())
  }
  useEffect(() => {
    if (!getInvestmentCategoryPending) {
      if (getInvestmentCategoryError) {

        return;
      }
      if (getInvestmentCategoryRes) {
        if (getInvestmentCategoryRes?.code === 200) {
          setInvestmentCategoryListData(getInvestmentCategoryRes?.data)
        }
      }
    }
  }, [
    getInvestmentCategoryPending,
    getInvestmentCategoryRes,
    getInvestmentCategoryError,
  ]);

  /* ---------------Investment Sub Category------------ */


  /* create Update Investment Sub Category */
  const handleCreateUpdateInvestmentSubCategory = (params) => {

    if (selectedInvestmentData?.subCategoryData) {
      params['_id'] = selectedInvestmentData?.subCategoryData?._id || selectedInvestmentData?.subCategoryData?.id
      dispatch(payrollActions.updateInvestmentSubCategoryRequest(params));
    }
    else {
      dispatch(payrollActions.addInvestmentSubCategoryRequest(params));
    }
  }
  useEffect(() => {
    if (!addInvestmentSubCategoryPending) {
      if (addInvestmentSubCategoryError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_ADD_INVESTMENT_SUB_CATEGORY_STORE"))
        clearInvestmentStoreData("sub_category")
        return;
      }
      if (addInvestmentSubCategoryRes) {
        if (addInvestmentSubCategoryRes?.code === 200) {
          handleOpenCloseModals("sub_category")
          clearInvestmentStoreData("sub_category")
          getInvestmentSubCategoryData()
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_ADD_INVESTMENT_SUB_CATEGORY_STORE"))
   
        }
      }
    }
  }, [
    addInvestmentSubCategoryPending,
    addInvestmentSubCategoryRes,
    addInvestmentSubCategoryError,
  ]);
  useEffect(() => {
    if (!updateInvestmentSubCategoryPending) {
      if (updateInvestmentSubCategoryError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_UPDATE_INVESTMENT_SUB_CATEGORY_STORE"))
        clearInvestmentStoreData("sub_category")
        return;
      }
      if (updateInvestmentSubCategoryRes) {
        if (updateInvestmentSubCategoryRes?.code === 200) {
          handleOpenCloseModals("sub_category")
          clearInvestmentStoreData("sub_category")
          getInvestmentSubCategoryData()
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_UPDATE_INVESTMENT_SUB_CATEGORY_STORE"))
  
        }
      }
    }
  }, [
    updateInvestmentSubCategoryPending,
    updateInvestmentSubCategoryRes,
    updateInvestmentSubCategoryError,
  ]);

  /* Get Investment Sub Categories*/
  const getInvestmentSubCategoryData = (pageNo) => {
    const payload = {
      page: pageNo || currentPage?.investmentSubCategoryCurrPage,
      limit: pagination?.limit
    }
    dispatch(payrollActions.getAllInvestmentSubCategoryRequest(payload))
  }
  useEffect(() => {
    if (!getAllInvestmentSubCategoryPending) {
      if (getAllInvestmentSubCategoryError) {

        return;
      }
      if (getAllInvestmentSubCategoryRes) {
        if (getAllInvestmentSubCategoryRes?.code === 200) {
          setInvestmentSubCategoryData(getAllInvestmentSubCategoryRes?.data)
        }
      }
    }
  }, [
    getAllInvestmentSubCategoryPending,
    getAllInvestmentSubCategoryRes,
    getAllInvestmentSubCategoryError,
  ]);

  useEffect(() => {
    if (!deleteInvestmentSubCategoryPending) {
      if (deleteInvestmentSubCategoryError) {
        dispatch(payrollActions.handleClearPayrollStore("HANDLE_DELETE_INVESTMENT_SUB_CATEGORY_STORE"))
        clearInvestmentStoreData("sub_category")
        return;
      }
      if (deleteInvestmentSubCategoryRes) {
        if (deleteInvestmentSubCategoryRes?.code === 200) {
          toggleDeleteInvestmentModal("sub_category")
          clearInvestmentStoreData("sub_category")
          dispatch(payrollActions.handleClearPayrollStore("HANDLE_DELETE_INVESTMENT_SUB_CATEGORY_STORE"))
          getInvestmentSubCategoryData()
        }
      }
    }
  }, [
    deleteInvestmentSubCategoryPending,
    deleteInvestmentSubCategoryRes,
    deleteInvestmentSubCategoryError,
  ]);

  /* Get Dropdown Master Constant Data */

  const getDropdownMasterConstantData = () => {
    dispatch(dropdownMasterActions?.getDropdownMasterConstantDataRequest())
  }

  useEffect(() => {
    if (!getDropdownMasterConstantDataPending) {
      if (getDropdownMasterConstantDataError) {
        return;
      }
      if (getDropdownMasterConstantDataRes) {
        if (getDropdownMasterConstantDataRes?.code === 200) {
          const dropdownDataConstantData = getDropdownMasterConstantDataRes?.data
          setAllDropdownConstantData(dropdownDataConstantData)
        }
      }
    }
  }, [
    getDropdownMasterConstantDataRes,
    getDropdownMasterConstantDataError,
    getDropdownMasterConstantDataPending
  ]);


  return (
    <>
      <CustomCards cardData={payrollSettingCard} />
      {activeCard === "salary_component" &&
        <div>
          <SalaryComponent
            handleToggleModals={handleToggleModals}
            earningData={earningData}
            deductionsData={deductionsData}

          />
        </div>
      }
      {activeCard === "pay_schedule" &&
        <div className="cardBox myRequest mb-3 employeeHistory">
          <PaySchedule
            appConstantData={appConstantData}
            upcomingPayrollData={upcomingPayrollData}
            handleToggleModals={handleToggleModals}
          />
        </div>
      }
      {
        activeCard === "it_declaration" &&
        <div className="cardBox myRequest mb-3 employeeHistory">
          <Investments
            handleOpenCloseModals={handleOpenCloseModals}
            investmentCategoryData={investmentCategoryData}
            toggleDeleteInvestmentModal={toggleDeleteInvestmentModal}
            investmentSubCategoryData={investmentSubCategoryData}
          />
        </div>
      }
      {activeCard === "tax_slab" &&
        <div className="cardBox myRequest mb-3 employeeHistory">
          <TaxSlab
            handleToggleModals={handleToggleModals}
            taxSlabData={taxSlabData}
            setCurrentPage={handlePageChange}
            toggleDeleteTaxSlabModal={toggleDeleteTaxSlabModal}
          />
        </div>
      }

      {/* Payroll Setting Modals */}

      {isOpenAddUpEarningModal && <AddUpdateEarnings
        openModel={isOpenAddUpEarningModal}
        handleOpenClose={handleToggleModals}
        addEarnings={addEarnings}
        allLeaveConstantData={allLeaveConstantData}
        allEarningTypeData={allEarningTypeData}
        allDeductionTypeData={allDeductionTypeData}
        getEarningTypeById={getEarningTypeById}
        isOpenModalType={isOpenModalType}
        addDeductions={addDeductions}
        earningDeductionSelectedData={earningDeductionSelectedData}
        updateEarnings={updateEarnings}
        updateDeductions={updateDeductions}
        earningTypeByIdData={earningTypeByIdData}
      />}


      {isOpenAddPreTaxDeductionModal && <AddPreTaxDeduction
        openModel={isOpenAddPreTaxDeductionModal}
        handleOpenClose={handleToggleModals}
      />}
      {isOpenAddPostTaxDeductionModal && <AddPostTaxDeduction
        openModel={isOpenAddPostTaxDeductionModal}
        handleOpenClose={handleToggleModals}
      />}

      {isOpenPayScheduleModal && <AddUpdatePaySchedule
        isOpenModal={isOpenPayScheduleModal}
        handleToggleModals={handleToggleModals}
        appConstantData={appConstantData}
        selectedPayScheduleData={selectedPayScheduleData}
        handleAddPayScheduleData={handleAddPayScheduleData}
        handleUpdatePaySchedule={handleUpdatePaySchedule}
      />}

      {isOpenTaxSlabModal && <AddUpdateTaxSlab
        isOpenModal={isOpenTaxSlabModal}
        handleToggleModals={handleToggleModals}
        selectedPayScheduleData={selectedPayScheduleData}
        selectedTaxSlabData={selectedTaxSlabData}
        handleAddUpdateTaxSlab={handleAddUpdateTaxSlab}
        filteredDropdownData={filteredDropdownData}

      />}

      {/* It Declaration */}
      {openModals?.isOpenSubCategoryModal && <AddUpdateSubCategory
        openModel={openModals?.isOpenSubCategoryModal}
        handleOpenClose={handleOpenCloseModals}
        selectedInvestmentSubCategory={selectedInvestmentData?.subCategoryData}
        investmentCategoryList={investmentCategoryListData}
        handleCreateUpdateInvestmentSubCategory={handleCreateUpdateInvestmentSubCategory}
        allDropdownConstantData={allDropdownConstantData}
      />}
      {openModals?.isOpenCategoryModal &&
        <AddUpdateCategory
          openModel={openModals?.isOpenCategoryModal}
          selectedInvestmentCategory={selectedInvestmentData?.categoryData}
          handleOpenClose={handleOpenCloseModals}
          handleCreateUpdateInvestmentCategory={handleCreateUpdateInvestmentCategory}
          allDropdownConstantData={allDropdownConstantData}
        />
      }

      {isShowDeleteTaxSlabModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelTaxSlab}
          isShow={isShowDeleteTaxSlabModal}
          desc={t("alerts.deletePopup.desc").replace('$1', "Tax Slab")}
          btnRightLoading={deleteTaxSlabPending}
          btnTextRight={t("btns.delete")}
          btnTextLeft={t("btns.cancel")}
        />)
      }
      {isShowDeleteInvestmentModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelInvestmentCatSub}
          isShow={isShowDeleteInvestmentModal}
          desc={t("alerts.deletePopup.desc").replace('$1', "Investment")}
          btnRightLoading={deleteTaxSlabPending}
          btnTextRight={t("btns.delete")}
          btnTextLeft={t("btns.cancel")}
        />)
      }

    </>
  )
}

export default PayrollSetting
