import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError } from '../../../globalComponents'
import { CustCheckbox, Input, Label } from '../../../commanComponent'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../../translation'
import { validateSchema } from '../../../../utils/ValidationHelper'
import { addUpdateInvestmentSubCategorySchema } from '../../../../utils/ValidationSchema'

const AddUpdateSubCategory = (props) => {
    const { t } = useCustomTranslation()
    // const { addInvestmentSubCategoryPending, updateInvestmentSubCategoryPending } = useSelector(state => state?.dropdownMaster)
    const {
        selectedInvestmentSubCategory,
        openModel,
        handleOpenClose,
        handleCreateUpdateInvestmentSubCategory,
        investmentCategoryList,
        allDropdownConstantData
    } = props

    const [errors, setErrors] = useState({})
    const [formData, setFormData] = useState({
        category: null,
        sub_category_name: null,
        input_type: null,
        max_limit: false,
        max_limit_amount: 0
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    const submitSubCategoryData = async () => {
        const validationResponse = await validateSchema(addUpdateInvestmentSubCategorySchema, formData);
        if (validationResponse.isValid) {
            const {
                category,
                sub_category_name,
                input_type,
                max_limit,
                max_limit_amount
            } = formData
            const params = {
                category_id:category?.value,
                sub_category_name,
                input_type:input_type?.value,
                max_limit,
                max_limit_amount
            }
            handleCreateUpdateInvestmentSubCategory(params)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            resetForm()
        })
    }, [openModel])

    useEffect(() => {
        if (selectedInvestmentSubCategory) {
            setFormData({
                category: { value: selectedInvestmentSubCategory?.category_id
                    , label: selectedInvestmentSubCategory?.category_name },
                sub_category_name: selectedInvestmentSubCategory?.sub_category_name,
                input_type: {value:selectedInvestmentSubCategory?.input_type,label:selectedInvestmentSubCategory?.input_type},
                max_limit: selectedInvestmentSubCategory?.max_limit,
                max_limit_amount: selectedInvestmentSubCategory?.max_limit_amount,
            })
        }
    }, [selectedInvestmentSubCategory])

    const resetForm = () => {
        setFormData({
            category: null,
            sub_category_name: null,
            input_type: null,
            max_limit: false,
            max_limit_amount: 0

        })
    }
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{selectedInvestmentSubCategory ?t("headings.updateInvestmentSubCategory") :  t("headings.addInvestmentSubCategory") }</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("sub_category")}
                    />
                </div>

                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.category")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={investmentCategoryList}
                                        placeholder={t("placeholders.selectCategory")}
                                        value={formData?.category || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "category")}
                                        name="category"
                                    />
                                    {errors?.category && (
                                        <CustomError text={errors.category} />
                                    )}
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.sub_category_name")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">

                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.sub_category_name")}
                                        value={formData?.sub_category_name}
                                        name="sub_category_name"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.sub_category_name && (
                                        <CustomError text={errors.sub_category_name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3"> 
                                    <Label text={t("labels.inputType")} required={true}/>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustomSelect
                                        data={allDropdownConstantData?.dropdownInputType}
                                        placeholder={t("placeholders.inputType")}
                                        value={formData?.input_type || ''}
                                        handleChange={(selectedOption) => handleFromChange(selectedOption, "input_type")}
                                        name="input_type"
                                    />
                                    {errors?.input_type && (
                                        <CustomError text={errors.input_type} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.max_limit")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"max_limit"}
                                        id={`max_limit`}
                                        checked={formData?.max_limit}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"max_limit"}
                                    />

                                </div>
                            </div>
                        </div>
                        {formData?.max_limit && <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.max_limit_amount")} required={true} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.max_limit_amount")}
                                        value={formData?.max_limit_amount}
                                        name="max_limit_amount"
                                        onChange={(e) => handleFromChange(e)}
                                    />
                                    {errors?.max_limit_amount && (
                                        <CustomError text={errors.max_limit_amount} />
                                    )}
                                </div>
                            </div>
                        </div>}
                        {/* <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        checked={formData?.is_active}
                                        handleChange={(e) => handleFromChange(e)}
                                        name={"is_active"}
                                    />

                                </div>
                            </div>
                        </div> */}

                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={selectedInvestmentSubCategory ? t("btns.update") : t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitSubCategoryData()}
                        // isLoading={selectedInvestmentSubCategory ? updateInvestmentSubCategoryPending : addInvestmentSubCategoryPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddUpdateSubCategory
