import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents'
import { Icon } from '../../../commanComponent';

const EarningRulesTable = ({ earningRuleData, deleteEarningRule,editEarningRule, rules}) => {

    const earningRuleField = [
        { key: 'sn', label: 'S.N' },
        { key: 'from', label: 'From' },
        { key: 'to', label: 'to' },
        { key: "amount", label: "Amount/Formula" },
        { key: 'action', label: 'Action' }
    ];
    const earningRuleScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        action: (item, index) => (
            <div >
                {rules && rules.length >0 &&<span
                    onClick={() => editEarningRule(item,index)}
                    className='border text-white bg-primary p-2 rounded' 
                    
                    style={{ cursor: "pointer" }}>

                    <Icon className={"fa fa-edit"} />
                </span>}
                <span
                    onClick={() => deleteEarningRule(index)}
                    className='border text-white bg-danger p-2 rounded' style={{ cursor: "pointer" }}>

                    <Icon className={"fa fa-trash"} />
                </span>

            </div>
        )
    }), [earningRuleData]);
    return (
        <div>
            {/* <CustomStrong text={"Earning Rules"} className="text-primary" /> */}
            <CustomTable
                data={earningRuleData}
                fields={earningRuleField}
                totalPages={earningRuleData?.totalPages}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none", border: "1px solid #F5F5F5" }}
                scopedSlots={earningRuleScopedSlots}
            />
            <hr />
        </div>
    )
}

export default EarningRulesTable
